      :filters="[
        {
          filterName: 'type_rep_2',
          filterType: 'select',
          inputWidth: '190px',
          labelText: 'Тип отчета',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          defaultValue: type,
          filterData: [
            { id: 0, name: 'срезовой' },
            { id: 1, name: 'моментальный' }
          ]
        },
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'time_rep_2',
          filterType: 'select',
          inputWidth: '120px',
          labelText: 'Время',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          defaultValue: time,
          filterData: this.$store.state.timeFilterCurrentValue
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle" v-if="tableTitle">
      Распределение грузовых локомотивов по Восточному полигону на
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
      <br />
      Электровозы депо КРСН, ВСИБ, ЗАБ, ДВС Ж.Д.
    </div>
    <DataTable
      :headers="headers"
      :url="getUrl"
      :params="{
        date: Math.floor(Date.now() / 1000),
        time_rep_2: this.$store.state.filtersValues['time_rep_2']
      }"
      ref="table"
      :tableType="tableType"
      :hasDetail="true"
      @loadDetail="setDetail"
    />
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
