<template>
  <v-app>
    <v-navigation-drawer
      id="nav"
      :mini-variant="mini"
      color="#00194D"
      app
      permanent
      :width="320"
      :class="mini ? 'nav-mini' : ''"
      floating
      mini-variant-width="80"
    >
      <div @click="home">
        <img class="ma-5" :src="require(`@/assets/images/${changeLogo}`)" />
      </div>
      <v-list v-if="!mini" dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-group
            v-if="item.children"
            v-model="item.model"
            :class="{
              'last-nested-active': isChildActive(item)
            }"
            :key="item.text"
            :prepend-icon="item.icon"
            :append-icon="item.arrow"
            :ripple="false"
            @click="closeChildren(item)"
          >
            <template v-slot:activator>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </template>

            <template v-for="child in item.children">
              <v-list-item
                v-model="child.model"
                class="plain-text"
                active-class="is-active"
                color="#00194d"
                :key="child.text"
                :to="{ path: child.path }"
                :ripple="false"
              >
                <v-list-item-content class="py-0">
                  <v-list-item-title>
                    {{ child.text }}
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </template>
          </v-list-group>
          <v-list-item
            v-else
            v-model="item.model"
            class="item-no-children"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
          >
            <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-0 my-0'">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content class="py-0">
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>

      <v-list v-else dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-item
            v-model="item.model"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
            @click="setChildPath(item)"
          >
            <v-tooltip right content-class="menu-tooltip">
              <template v-slot:activator="{ on, attrs }">
                <v-list-item-action
                  v-bind="attrs"
                  v-on="on"
                  :class="mini ? 'ma-0' : 'mr-2 my-0'"
                >
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-item-action>
              </template>
              <span class="menu-tooltip-content">
                {{ item.text }}
              </span>
            </v-tooltip>
          </v-list-item>
        </template>
      </v-list>

      <MenuFooter />
    </v-navigation-drawer>
    <v-main id="main">
      <div @mousemove="mouseMove($event)" @mouseup.left="mouseUpLeft($event)">
        <v-btn
          class="toggle-btn"
          :class="{ 'is-closed': mini }"
          @click="toggle"
          dark
          small
        >
          <v-icon>icon-arrow-dropdown</v-icon>
        </v-btn>
        <router-view></router-view>
        <Footer />
      </div>
    </v-main>
  </v-app>
</template>

<script>
import Footer from './components/Footer';
import MenuFooter from './components/MenuFooter';
export default {
  name: 'App',
  components: { MenuFooter, Footer },
  data: () => {
    return {
      mini: false
    };
  },
  created() {
    if (this.$route.path === '/') this.$router.push('/tasks/');
  },
  methods: {
    home() {
      this.$router.push('/tasks/');
    },
    setChildPath(item) {
      if (item.children) {
        this.$router.push(item.children[0].path);
      }
    },
    toggle() {
      this.$store.state.menuMini = this.mini = !this.mini;
      if (!this.mini) {
        this.$store.state.menuItems.forEach((parentElement) => {
          parentElement.model =
            this.$route.path.indexOf(parentElement.path) >= 0;
          if (parentElement.children) {
            parentElement.children.forEach((childElement) => {
              childElement.model =
                this.$route.path.indexOf(childElement.path) >= 0;
            });
          }
        });
      }
    },
    closeChildren(item) {
      if (!this.mini) {
        this.$store.state.menuItems.forEach((parentElement) => {
          if (parentElement.path === item.path && parentElement.children) {
            parentElement.children.forEach((childElement) => {
              childElement.model = false;
            });
          }
        });
      }
    },
    isChildActive(item) {
      let isActive = false;
      for (let parent of this.$store.state.menuItems) {
        if (parent.path === item.path) {
          isActive = this.$route.path.indexOf(parent.path) >= 0;
        } else {
          if (parent.children) {
            for (let child of parent.children) {
              if (child.path === item.path) {
                isActive = this.$route.path.indexOf(child.path) >= 0;
              }
            }
          }
        }
      }
      return isActive;
    },
    mouseMove(e) {
      if (this.$store.state.resizing) {
        let tableContentHeight = window.tableContent.clientHeight,
          tableDetailHeight = window.tableDetail.clientHeight,
          mouseDiff = e.y - this.$store.state.mouseY;

        if (
          tableDetailHeight - mouseDiff >=
            this.$store.state.tableDetailMinHeight &&
          tableContentHeight + mouseDiff >=
            this.$store.state.tableContentMinHeight
        ) {
          window.tableContent.style.height = window.tableContent.children[0].children[0].style.height =
            tableContentHeight + mouseDiff + 'px';
          window.tableDetail.style.height = window.tableDetail.children[0].children[0].style.height =
            tableDetailHeight - mouseDiff + 'px';
          this.$store.state.mouseY += mouseDiff;
        }
      }
    },
    mouseUpLeft(e) {
      this.$store.state.resizing = false;
      this.$store.state.mouseY = e.y;
    }
  },
  computed: {
    changeLogo() {
      return this.mini ? 'logo-mini.svg' : 'logo.svg';
    }
  }
};
</script>

