<template>
  <v-expansion-panels v-model="panel" focusable flat class="table-filters">
    <v-expansion-panel>
      <v-expansion-panel-header
        @click="changeFilter()"
        hide-actions
        :ripple="false"
      >
        <span class="mr-2 icon icon-funnel"></span>
        <span>Фильтры</span>
        <span class="icon icon-arrow-dropdown"></span>
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-row dense class="filter-block">
          <template v-for="filter in filters">
            <DateFilter
              :key="filter.filterName"
              :ref="`${filter.filterName}`"
              v-if="filter.filterType === 'date'"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
            />
            <SelectFilter
              :key="filter.filterName"
              :defaultValue="filter.defaultValue"
              :ref="`${filter.filterName}`"
              :isRemote="filter.isRemote"
              :localItems="filter.filterData"
              :filterUrl="filter.filterUrl"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
              v-if="filter.filterType === 'select'"
              :keyText="filter.keyText"
              :keyValue="filter.keyValue"
              :inputWidth="filter.inputWidth"
            />
            <MultipleSelectFilter
              :key="filter.filterName"
              :defaultValue="filter.defaultValue"
              :ref="`${filter.filterName}`"
              :isRemote="filter.isRemote"
              :localItems="filter.filterData"
              :filterUrl="filter.filterUrl"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
              v-if="filter.filterType === 'multiple_select'"
              :keyText="filter.keyText"
              :keyValue="filter.keyValue"
              :inputWidth="filter.inputWidth"
            />
            <SelectTreeFilter
              :key="filter.filterName"
              v-if="filter.filterType === 'select_tree'"
            />
            <RangeDateFilter
              :key="filter.filterName"
              :ref="`${filter.filterName}`"
              v-if="filter.filterType === 'range_date'"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
            />
            <ChooseDialog
              v-if="filter.filterType === 'choose_btn'"
              :key="filter.filterName"
            />
            <FilterCheckbox
              :label-text="filter.labelText"
              v-if="filter.filterType === 'checkbox'"
              :key="filter.filterName"
              :filterName="filter.filterName"
              :disableFilterName="filter.disableFilterName"
              @setDisabled="$emit('setDisabled', $event, filter.filterName)"
            />
          </template>
        </v-row>
        <FilterButtons
          @updateTable="$emit('reloadTable')"
          @resetFilters="resetAll()"
        />
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import DateFilter from './inputs/DateFilter';
import SelectFilter from './inputs/SelectFilter';
import FilterButtons from './buttons/FilterButtons';
import MultipleSelectFilter from './inputs/MultipleSelectFilter';
import RangeDateFilter from './inputs/RangeDateFilter';
import SelectTreeFilter from './inputs/SelectTreeFilter';
import ChooseDialog from './ChooseDialog';
import FilterCheckbox from './inputs/FilterCheckbox';

export default {
  name: 'Filters',
  components: {
    FilterCheckbox,
    ChooseDialog,
    DateFilter,
    SelectFilter,
    FilterButtons,
    MultipleSelectFilter,
    RangeDateFilter,
    SelectTreeFilter
  },
  props: ['filters'],
  data() {
    return {
      panel: 0
    };
  },
  methods: {
    resetAll() {
      for (let filter in this.$refs) {
        if (
          this.$store.state.defaultFiltersValues &&
          this.$store.state.defaultFiltersValues[filter]
        ) {
          if (filter === 'date')
            this.$store.state.defaultFiltersValues[
              filter
            ] = new Date().toISOString().substr(0, 10);

          this.$refs[filter][0].value = this.$store.state.defaultFiltersValues[
            filter
          ];
        }
      }
    },
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    }
  },
  created() {
    this.$store.state.filterOpen = true;
  }
};
</script>

<style scoped lang="sass">
.table-filters
  margin-bottom: 20px

.v-expansion-panel-header
  padding: 9px 20px 8px 14px
  line-height: $line-height-root
  color: $color-white
  background-color: $color-blue
  border-radius: 4px
  transition: background-color $transition-normal
  &:before
    content: none
  &:hover
    background-color: #30446E
  & > *:not(.v-expansion-panel-header__icon)
    flex: none
  .icon-funnel
    padding-right: 1px
    font-size: 18px
  .icon-arrow-dropdown
    transform: rotate(0deg) translateY(1px)
    margin-left: 14px
    font-size: 11px
    transition: transform $transition-normal
  &--active
    .icon-arrow-dropdown
      transform: rotate(180deg) translateY(0px)

.filter-block
  width: 90%
</style>
