<template>
  <div>
    <p class="filter-label">{{ labelText }}</p>
    <v-select
      v-model="value"
      :items="items"
      color="#FF7141"
      item-text="name"
      item-value="id"
      :style="{
        width: inputWidth
      }"
      solo
      flat
      hide-details
      multiple
      @change="changeValue()"
    >
    </v-select>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'MultipleSelectFilter',
  created() {
    if (this.isRemote) {
      this.fetchItems();
    } else this.items = this.localItems;
    if (this.defaultValue) this.value = this.defaultValue;
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  methods: {
    fetchItems() {
      axios.get(this.filterUrl).then((response) => {
        this.items = response.data.data;
      });
    },
    changeValue() {
      this.$store.state.filtersValues[this.filterName] = this.value;
    }
  },
  props: [
    'labelText',
    'filterName',
    'isRemote',
    'filterUrl',
    'localItems',
    'inputWidth',
    'defaultValue'
  ],
  data() {
    return {
      value: [],
      items: []
    };
  }
};
</script>

