<template>
  <v-card
    v-resize="onResize"
    id="table"
    class="table flex"
    elevation="0"
    :class="[
      {
        'slide-leave slide-enter-active': this.$store.state.sheet && hasDetail,
        'slide-leave-to slide-leave-active': !this.$store.state.sheet && hasDetail
      },
      tableType
    ]"
    :style="getTableHeight"
  >
    <div
      class="v-data-table pl-2 v-data-table--dense v-data-table--fixed-height theme--light"
    >
      <div class="v-data-table__wrapper" :style="getTableHeight">
        <table class="v-data-table-body">
          <thead class="v-data-table-header">
            <tr v-for="(row, id) in headerRows" :key="id">
              <th
                v-for="(h, i) in row"
                :key="i"
                :class="getAlign(h, h.textMinify)"
                :rowspan="h.children ? 1 : headerDeep"
                :colspan="getColspan(h, id)"
              >
                {{ h.text | getHeaderText }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              :style="
                item['tr_bgcolor']
                  ? { 'background-color': item['tr_bgcolor'] }
                  : ''
              "
              v-for="(item, i) in tableData"
              :key="i"
              @click="[selectRow(i), collapseRow(i, item)]"
              :class="{ 'selected-row': i === selectedItem }"
            >
              <td
                v-for="(header, col) in headersFlat"
                :key="col"
                :class="[
                  cellClasses[i + '_' + header.value],
                  {
                    'close-group':
                      item[header.value + '_grouping'] &&
                      item[header.value] &&
                      groupingRowsIndClose.some((el) => el === i),
                    'hide-cell': !item.grouping_row && setCollapse(i)
                  }
                ]"
                :style="setRepCellBG(item, header)"
                :rowspan="setRowspan(item, header.value)"
                :colspan="
                  item[header.value + '_colspan']
                    ? item[header.value + '_colspan']
                    : 0
                "
                @click="
                  openDetail(
                    $event,
                    item[header.value + '_request'],
                    header.value,
                    i,
                    header,
                    item
                  )
                "
              >
                <v-tooltip
                  right
                  v-if="item[header.value + '_tooltip']"
                  max-width="190"
                  open-delay="2"
                  transition="fade-transition"
                >
                  <template v-slot:activator="{ on }">
                    <div
                      :style="{ width: setWidth(header.width, item) }"
                      v-on="on"
                      v-if="
                        isEditable(item, header.editable, item[header.value])
                      "
                    >
                      <EditCell
                        @updateCell="
                          changeClass(
                            $event,
                            item[header.value],
                            item,
                            header,
                            i
                          )
                        "
                        :oldValue="item[header.value]"
                        :row="item"
                        :col="header"
                        :rowId="i"
                      />
                    </div>
                    <div
                      :style="{ width: setWidth(header.width, item) }"
                      v-on="on"
                      v-else
                      :class="{
                        'detail-cell':
                          selectedCell.i === i &&
                          selectedCell.dataIndex === header.value
                      }"
                    >
                      {{
                        item[header.value]
                          | customizeValue(
                            header.value,
                            item,
                            tableType,
                            tableData,
                            i,
                            header.formatDate
                          )
                      }}
                    </div>
                  </template>
                  <span>{{
                    getTooltip(
                      item[header.value + '_tooltip'],
                      item[header.value]
                    )
                  }}</span>
                </v-tooltip>
                <template v-else>
                  <EditCell
                    @updateCell="
                      changeClass($event, item[header.value], item, header, i)
                    "
                    :oldValue="item[header.value]"
                    :row="item"
                    :col="header"
                    :rowId="i"
                    v-if="
                      isEditable(item, header.editable, item[header.value]) &&
                      header.editType !== 'date'
                    "
                  />
                  <AddTableButton
                    v-else-if="header.editType === 'button'"
                    :record="item"
                    @forceUpdateTable="$emit('forceUpdate')"
                  />
                  <EditCellDate
                    v-else-if="header.editType === 'date'"
                    @updateCell="
                      changeClass($event, item[header.value], item, header, i)
                    "
                    :oldValue="item[header.value]"
                    :row="item"
                    :col="header"
                    :rowId="i"
                  />
                  <div v-else>
                    <v-checkbox
                      @click="setCompare(item)"
                      color="#4D6C99"
                      :disabled="setCheckDisabled(item)"
                      hide-details
                      :ripple="false"
                      class="mb-1"
                      v-if="
                        tableType === 'compare' && header.value === 'checkbox'
                      "
                    ></v-checkbox>
                    <div
                      :style="{ width: setWidth(header.width, item) }"
                      v-else
                      :class="{
                        'detail-cell':
                          selectedCell.i === i &&
                          selectedCell.dataIndex === header.value
                      }"
                    >
                      {{
                        item[header.value]
                          | customizeValue(
                            header.value,
                            item,
                            tableType,
                            tableData,
                            i,
                            header.formatDate
                          )
                      }}
                    </div>
                  </div>
                </template>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <v-progress-circular
      v-if="progress"
      class="progress-circle"
      indeterminate
      color="#00194D"
    ></v-progress-circular>
  </v-card>
</template>

<script>
import axios from 'axios';
import EditCell from './inputs/EditCell';
import EditCellDate from './inputs/EditCellDate';
import AddTableButton from './buttons/AddTableButton';

export default {
  name: 'DataTable',
  components: {
    AddTableButton,
    EditCellDate,
    EditCell
  },
  created() {
    if (!this.isDetail) {
      this.$store.state.defaultFiltersValues = Object.assign({}, this.params);
      if (this.tableType === 'compare') {
        this.fetchTableData(this.params);
      }
      this.setHeadersDefault();
      this.$store.state.sheet = false;
    }
  },
  filters: {
    getHeaderText(text) {
      let headText = text;
      if (text.indexOf('<br>') >= 0) {
        headText = text.replace('<br>', '\n');
      }
      return headText;
    },
    customizeValue(value, col, record, tableType, data, recInd, formatDate) {
      let val = value;
      if (
        (tableType === 'norm_depo' && col === 'sort_number') ||
        (tableType === 'plan_ind' && col === 'row_number')
      ) {
        if (
          record.type === '' ||
          (tableType === 'plan_ind' && !record.grouping_row)
        ) {
          let ind = 0;
          data.every(function (rec) {
            if (
              rec.type === '' ||
              (tableType === 'plan_ind' && !rec.grouping_row)
            ) {
              ind++;
            }
            return record !== rec;
          });
          return ind;
        } else return '';
      }
      if (typeof value === 'string') {
        if (value.indexOf('<br>') >= 0) {
          val = value.split('<br>').join('\n ');
        }
        if (value.indexOf('/') >= 0 && +value.split('/').join('')) {
          val = value.split('/').join(' /\n');
        }
      }
      if (tableType !== 'plan_ind' && col === 'row_number') {
        //значения для простого нумерованного столбца
        let i = 0;
        data.every(function (rec) {
          i++;
          return record !== rec;
        });
        return i;
      }

      if (tableType === 'plan_ind' && record.grouping_row) {
        if (value) {
          val = value.split('.')[1].trim();
        }
      }

      if (tableType === 'norm_ratio')
        if (typeof value === 'number') return Math.floor(val * 100) / 100;
      if (typeof value === 'string' && value.indexOf('<b>') >= 0) {
        if (value.indexOf('</b>') >= 0) {
          val = value.substring(3, value.length - 4).trim();
        } else {
          let tempVal = value.substring(3, value.length - 3).trim();
          if (tempVal.indexOf('<b>') >= 0) {
            val = tempVal.substring(3, tempVal.length - 3).trim();
          } else val = tempVal;
        }
      }
      if (typeof value === 'string' && value.indexOf('<big>') >= 0) {
        val = value.substring(5, value.length - 6).trim();
        if (val.indexOf('<b>') >= 0) {
          val = val.substring(3, val.length - 3).trim();
        }
      }

      if (
        tableType === 'st_tasks' &&
        (col === 'road' || col === 'station_name')
      ) {
        if (recInd - 1 >= 0 && record[col] === data[recInd - 1][col]) val = '';
      }

      if (
        tableType === 'mod_nsi' &&
        (col === 'start_date' || col === 'end_date')
      ) {
        let dateSrt = new Date(value);
        val =
          (dateSrt.getHours() < 10 ? '0' : '') +
          dateSrt.getHours() +
          ':' +
          (dateSrt.getMinutes() < 10 ? '0' : '') +
          dateSrt.getMinutes() +
          ' ' +
          (dateSrt.getDate() < 10 ? '0' : '') +
          dateSrt.getDate() +
          '.' +
          (dateSrt.getMonth() < 10 ? '0' : '') +
          (dateSrt.getMonth() + 1) +
          '.' +
          dateSrt.getFullYear();
      }

      if (
        col === 'date_start' &&
        (tableType === 'rep_control_0' || tableType === 'rep_control_1')
      ) {
        const [year, month, day] = value.split(' ')[0].split('-');
        val = `${day}.${month}.${year}`;
      }
      if (formatDate) {
        if (value) {
          let dateTimeArr = value.split(' ');
          const [year, month, day] = dateTimeArr[0].split('-');
          const [hours, minutes] = dateTimeArr[1].split(':');
          val = `${day}.${month}.${year} ${hours}:${minutes}`;
        }
      }
      if ((tableType === 'rep_19_4' || tableType === 'rep_19_5') && value === 0)
        val = '';

      return val;
    }
  },
  computed: {
    getTableHeight() {
      if (this.tableType !== 'compare') {
        const FILTERS_CONTENT_HEIGHT =
            this.tableType === 'plan_main' || this.tableType === 'rep_18'
              ? 222
              : 138,
          BLOCKS_HEIGHT = 276,
          DETAIL_HEIGHT = 65,
          OPEN_DETAIL_CONTENT = 190,
          INFO_HEIGHT = 30;
        let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

        if (this.hasDetail) tableHeight -= DETAIL_HEIGHT;
        if (this.hasInfo) tableHeight -= INFO_HEIGHT;

        if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
        if (this.$store.state.sheet) tableHeight -= OPEN_DETAIL_CONTENT;

        if (this.isDetail) tableHeight = 185;

        return `height: ${tableHeight}px`;
      } else return 'height: 200px';
    }
  },
  methods: {
    setHeadersDefault() {
      if (this.headers) {
        if (!this.headers.some((h) => h.value === 'extra')) {
          this.headers.push({
            text: '',
            value: 'extra'
          });
        }
      }

      let rowsArr = [];
      this.headerDeep = this.deepSearch(
        this.headers || this.headersRemote,
        0,
        []
      );
      if (isFinite(this.headerDeep)) {
        for (let i = 1; i <= this.headerDeep; i++) {
          let row = this.getSubHeader(
            this.headers || this.headersRemote,
            i,
            0,
            []
          );
          rowsArr.push(row);
        }
      }
      this.headerRows = rowsArr;
    },
    setCheckDisabled(record) {
      return (
        this.$store.state.planToCompare.length === 2 &&
        !this.$store.state.planToCompare.find((el) => el === record.id)
      );
    },
    setCompare(record) {
      if (
        this.$store.state.planToCompare.length > 0 &&
        this.$store.state.planToCompare.find((el) => el === record.id)
      ) {
        this.$store.state.planToCompare.splice(
          this.$store.state.planToCompare.indexOf(record.id),
          1
        );
      } else {
        this.$store.state.planToCompare.push(record.id);
      }
    },
    setWidth(w, it) {
      let width = '';
      if (!it.grouping_row && w) {
        width = w + 'px';
      }
      return width;
    },
    resetData() {
      if (this.tableData.length > 0) this.tableData = [];
    },
    setRowspan(row, dataIndex) {
      let rowspan = '';
      if (dataIndex && dataIndex.indexOf('_') >= 0) {
        let dataIndexArr = dataIndex.split('_');
        let rowspanDataIndex =
          dataIndexArr.slice(0, -1).join('_') + '_rowspan_origin';
        if (row[rowspanDataIndex]) {
          rowspan = row[rowspanDataIndex];
        }
      }

      return rowspan;
    },
    checkRowspanRecord(record) {
      return Object.keys(record).some((key) => key.indexOf('rowspan') >= 0);
    },
    changeClass(val, oldVal, it, col, i) {
      this.setCellStyles(it, i, col, val);
      this.changeValue(val, col.value, i, oldVal);
      this.$store.state.cancelBtnActive = !this.editedValues.length > 0;
      this.$store.state.recalcBtnActive = !this.editedValues.length > 0;
      this.$forceUpdate();
    },
    changeValue(value, col, id, oldValue) {
      let isValueExist;
      this.editedValues.forEach((editedValue, i, arr) => {
        if (editedValue.id === id && editedValue.col === col) {
          if (+value !== +oldValue) {
            editedValue.value = value;
          } else {
            arr.splice(i);
          }
          isValueExist = true;
        }
      });
      if (!isValueExist) {
        this.editedValues.push({ value, col, id });
      }
    },
    highlightCell(i, col, it) {
      if (
        (it[col.value + '_request'] || col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] || it[col.value] === 0)
      ) {
        if (
          this.selectedCell.i === i &&
          this.selectedCell.dataIndex === col.value
        ) {
          this.selectedCell.i = null;
          this.selectedCell.dataIndex = null;
          this.$store.state.sheet = false;
        } else {
          this.selectedCell.i = i;
          this.selectedCell.dataIndex = col.value;
        }
      }
    },
    openDetail(e, url, key, i, col, it) {
      this.highlightCell(i, col, it);
      if (url || key === 'duration') {
        if (
          this.selectedCell.i === i &&
          this.selectedCell.dataIndex === col.value
        ) {
          setTimeout(
            () =>
              e.target.scrollIntoView({
                block: 'end',
                inline: 'nearest',
                behavior: 'smooth'
              }),
            50
          );
          this.$store.state.sheet = true;
          this.$emit('loadDetail', url || '');
        }
      }
    },
    selectRow(rowNumber) {
      this.selectedItem = rowNumber;
    },
    collapseRow(i) {
      if (!this.groupingRowsIndClose.some((el) => el === i)) {
        this.groupingRowsIndClose.push(i);
      } else
        this.groupingRowsIndClose = this.groupingRowsIndClose.filter(
          (el) => el !== i
        );
    },
    getAlign(header, info) {
      let cl = '';
      if (info === 'mini') {
        cl += 'smaller-text ';
      }
      if (header.border || header.value === 'extra') {
        cl += 'thick-border-bottom ';
      } else cl += 'thin-border-bottom ';
      if (!header.alignHeader) {
        cl += 'align-text-left ';
      }
      if (header.fixedColumn) cl += 'fixed-column ';
      return cl;
    },
    getTooltip(tooltipText, value) {
      if (tooltipText) {
        if (
          this.tableType === 'norm_sec' ||
          this.tableType === 'reg_join' ||
          this.tableType === 'reg_sec'
        ) {
          let ODZ = tooltipText.split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            originValue,
            approveUser = '';

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (approveUser && originValue && value !== originValue) {
              return (
                'Данные изменены: ' +
                approveUser +
                ' с ' +
                originValue +
                ' на ' +
                value +
                '; мин = ' +
                minODZ +
                '; макс = ' +
                maxODZ
              );
            } else {
              return 'мин = ' + minODZ + '; макс = ' + maxODZ;
            }
          } else {
            return 'мин = ' + minODZ + '; макс = ' + maxODZ;
          }
        } else {
          if (this.tableType === 'mod_nsi') {
            let toolArr = tooltipText.split(';'),
              approveUs = '',
              originNumber = '';
            if (toolArr[0].split(' = ')[1] !== '')
              originNumber = toolArr[0].split(' = ')[1];
            if (toolArr[1].split(' = ')[1] !== '')
              approveUs = toolArr[1].split(' = ')[1];

            return (
              'Данные изменены: ' +
              approveUs +
              ' c ' +
              originNumber +
              ' на ' +
              value
            );
          } else return tooltipText;
        }
      } else return '';
    },
    isEditable(it, ed, value) {
      return (
        (value !== undefined || this.tableType === 'rep_control_0') &&
        ed &&
        it.type !== 'S' &&
        it.type !== 'SS' &&
        it.type !== 'I'
      );
    },
    setCellStyles(it, ind, col, newValue) {
      let cl_string = '';
      if (
        +it[col.value] ||
        +it[col.value] === 0 ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<big>') >= 0) ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<b>') >= 0 &&
          (+it[col.value].substring(3, it[col.value].length - 4) ||
            +it[col.value].substring(3, it[col.value].length - 4) === 0)) ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<b><b>') >= 0 &&
          (+it[col.value].substring(6, it[col.value].length - 6) ||
            +it[col.value].substring(6, it[col.value].length - 6) === 0)) ||
        it.grouping_row ||
        col.isBigText ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('+') >= 0 &&
          +it[col.value].split('+')[0])
      ) {
        cl_string += 'numbers-font-size ';
      }
      let dataIndexArr = col.value.split('_');
      let rowspanDataIndex =
        dataIndexArr.slice(0, -1).join('_') + '_rowspan_origin';
      if (it[rowspanDataIndex] === 0) {
        cl_string += 'hide-cell not-colored ';
      }
      if (it.grouping_row && !it[col.value + '_colspan'])
        cl_string += 'hide-cell ';
      if (
        (col.editable ||
          it[col.value + '_request'] ||
          col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] ||
          +it[col.value] === 0 ||
          this.tableType === 'rep_control_0')
      ) {
        if (it[col.value + '_request'] || col.value === 'duration') {
          cl_string += 'detail-cell-main ';
        } else {
          if (newValue && +newValue !== +it[col.value]) {
            cl_string += 'edited-cell-main ';
          } else cl_string += 'edit-cell-main ';
        }
      }
      if (it[col.value + '_grouping'] && it[col.value])
        cl_string += 'grouping-cell-open ';
      if (this.tableType === 'reg_join') {
        if (col.editable && it[col.value] === 0) cl_string += 'back-pink ';
      }
      if (
        (!this.checkRowspanRecord(it) && ind !== 0 && ind % 2 !== 0) ||
        (this.checkRowspanRecord(it) &&
          this.checkRowspanInd(ind) % 2 !== 0 &&
          this.checkRowspanInd(ind) !== 0 &&
          isFinite(this.checkRowspanInd(ind)))
      ) {
        if (col.fixedColumn) {
          cl_string += 'fixed-colored ';
        } else {
          cl_string += 'colored-row ';
        }
      } else {
        if (col.fixedColumn) cl_string += 'fixed-column ';
      }
      if (it[col.value + '_color']) {
        switch (it[col.value + '_color']) {
          case 'green':
            cl_string += 'colored-cell-green ';
            break;
          case 'red':
            cl_string += 'colored-cell-red ';
            break;
          case 'yellow':
            cl_string += 'back-yellow ';
            break;
        }
      }
      if (
        it.type === 'GG' ||
        it.type === 'SS' ||
        it.type === 'G' ||
        it.type === 'S' ||
        it.type === 'I' ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<b>') >= 0 &&
          it[col.value].indexOf('<big>') < 0)
      )
        cl_string += 'bold-text ';
      if (col.rightBorder) cl_string += 'right_border ';
      if (it[col.value + '_tooltip'] && this.tableType !== 'norm_depo') {
        if (
          typeof it[col.value + '_tooltip'] === 'string' &&
          it[col.value + '_tooltip'].indexOf(';') >= 0
        ) {
          if (this.tableType === 'mod_nsi') {
            let toolArr = it[col.value + '_tooltip'].split(';'),
              originNumber = '';
            if (toolArr[0].split(' = ')[1] !== '')
              originNumber = toolArr[0].split(' = ')[1];
            if (+it[col.value] !== +originNumber) {
              cl_string += 'edited-cell-main ';
            }
          } else {
            let ODZ = it[col.value + '_tooltip'].split(';'),
              minODZ = Number(ODZ[0].replace(/\D+/g, '')),
              maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
              maxODZ10 = Math.ceil(maxODZ * 1.1),
              originValue,
              approveUser = '',
              value = newValue ? newValue : it[col.value];
            if (Number(ODZ[2].replace(/\D+/g, '')))
              originValue = Number(ODZ[2].replace(/\D+/g, ''));
            if (ODZ[3].split(' = ')[1] !== '')
              approveUser = ODZ[3].split(' = ')[1];

            if (ODZ) {
              if (
                approveUser &&
                (originValue || originValue === 0) &&
                value !== originValue
              ) {
                if (
                  !(value >= minODZ && value <= maxODZ) &&
                  (value === 0 || value > maxODZ10)
                ) {
                  cl_string += 'back-pink edited-cell-main ';
                } else if (
                  !(value >= minODZ && value <= maxODZ) &&
                  (value === 0 || value > maxODZ10)
                ) {
                  cl_string += 'back-pink ';
                } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                  cl_string += 'back-yellow edited-cell-main ';
                }
              } else {
                if (
                  !(value >= minODZ && value <= maxODZ) &&
                  (value === 0 || value > maxODZ10)
                ) {
                  cl_string += 'back-pink edit-cell-main ';
                } else if (
                  !(value >= minODZ && value <= maxODZ) &&
                  (value === 0 || value > maxODZ10)
                ) {
                  cl_string += 'back-pink ';
                } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                  cl_string += 'back-yellow edit-cell-main ';
                }
              }
            }
          }
        }
      }
      if (this.tableType === 'plan_main') {
        cl_string += 'wrap-cell vertical-align-text ';
      } else if (this.tableType === 'rep_tp_norm') {
        cl_string += 'wrap-cell ';
      }

      if (this.tableType.includes('rep')) {
        if (
          ((col.value === 'DELTA8' ||
            col.value === 'DELTA11' ||
            col.value === 'DELTA14') &&
            +it[col.value] < 0) ||
          ((col.value === 'DELTA36' || col.value === 'DELTA40') &&
            +it[col.value] > 0)
        ) {
          cl_string += 'back-pink ';
        }
      }
      this.cellClasses[ind + '_' + col.value] = cl_string;
    },
    fetchTableData(params, url) {
      this.progress = true;
      if (!this.isDetail || (this.isDetail && this.$store.state.sheet)) {
        axios
          .get(this.url || url, {
            params: params
          })
          .then((response) => {
            this.resetData();
            if (this.$store.state.tableStores[this.tableType])
              delete this.$store.state.tableStores[this.tableType];
            this.progress = false;
            if ((params && params.load) || this.isDetail) this.setHeadersDefault();

            if (!this.isRemote || (this.isRemote && params.load)) {
              if (!this.isRemote) this.createHeadersFlat();

              if (
                response.data.data.some((row) =>
                  this.groupKey ? row[this.groupKey] : row.group
                )
              ) {
                this.tableData = this.createGroupingData(
                  response.data.data,
                  this.headersFlat[0].value !== 'row_number'
                    ? this.headersFlat[0].value
                    : this.headersFlat[1].value,
                  this.groupKey ? this.groupKey : 'group'
                );
                this.setGroupingRowsInd();
              } else this.tableData = response.data.data;

              this.setColspanRowColor();
              this.setClasses();
            } else {
              this.createHeaders(response.data.settings);
              params.load = true;
              this.fetchTableData(params, url);
            }
            this.$store.state.tableStores[this.tableType] = this.tableData;
          });
      }
    },
    setClasses() {
      this.tableData.forEach((row, i) => {
        this.headersFlat.forEach((col) => {
          this.setCellStyles(row, i, col);
        });
      });
    },
    setRepCellBG(item, header) {
      let color = '';
      let cellValue = item[header.value];

      if (this.tableType && this.tableType === 'rep_tp_norm') {
        if (cellValue && cellValue.includes('/')) {
          let splitValues = cellValue.split(',')[1].split('/');
          splitValues[1] = splitValues[1].split(' ')[0];

          if (+splitValues[0] === 0) {
            color = '#FAC8C8';
          } else if (+splitValues[0] > 0 && +splitValues[0] < +splitValues[1]) {
            color = '#FCFFDF';
          } else if (+splitValues[0] === +splitValues[1]) {
            color = '#D8F5F3';
          }
        }
      } else if (this.tableType && this.tableType.includes('rep')) {
        color = item[header.value + '_color'];
      }
      return { 'background-color': color };
    },
    setHeaders(settings, config) {
      for (let key in settings) {
        let headItem = {};
        if (settings[key].visible) {
          if (settings[key].child) {
            headItem = {
              text: settings[key].name,
              alignHeader: 'center',
              value: key,
              children: []
            };
            this.setHeaders(settings[key].child, headItem.children);
          } else {
            headItem = {
              text: settings[key].name,
              alignHeader: 'center',
              value: key,
              width: settings[key].width,
              border: true,
              fixedColumn: settings[key].fixedColumn
            };
          }
          config.push(headItem);
        }
      }
      return config;
    },
    createHeaders(settings) {
      this.headersRemote = this.setHeaders(settings, []);
      if (!this.headersRemote.some((h) => h.value === 'extra')) {
        this.headersRemote.push({
          text: '',
          value: 'extra'
        });
      }
      this.createHeadersFlat();
    },
    createHeadersFlat() {
      let headersArray = this.headers || this.headersRemote;
      this.headersFlat = [];
      this.setHeadersFlat(headersArray);
    },
    setHeadersFlat(headersArray) {
      for (let key in headersArray) {
        if (headersArray[key].children) {
          this.setHeadersFlat(headersArray[key].children);
        } else this.headersFlat.push(headersArray[key]);
      }
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getSubHeader(headers, rowId, counter, arr) {
      counter++;
      if (rowId === counter) {
        headers.forEach((v) => {
          if (rowId > 1) {
            arr.push(v);
          } else arr = arr.concat(v);
        });
      } else {
        headers
          .filter((i) => i.children)
          .forEach((v) => {
            this.getSubHeader(v.children, rowId, counter, arr);
          });
      }
      return arr;
    },
    deepSearch(obj, counter, arr) {
      counter++;
      for (let key in obj) {
        if (obj[key].children) {
          this.deepSearch(obj[key].children, counter, arr);
        }
        if (arr) arr.push(counter);
      }
      return Math.max.apply(null, arr);
    },
    calculateColspan(obj, arr) {
      if (obj.children) {
        let chLength = obj.children.length;
        obj.children.forEach((c) => {
          if (c.children) {
            chLength -= 1;
            this.calculateColspan(c, arr);
          }
        });
        arr.push(chLength);
      }
      return arr.reduce((res, el) => res + el);
    },
    getColspan(col) {
      let colspan;
      if (col.children) {
        colspan = this.calculateColspan(col, []);
      } else colspan = 1;
      return colspan;
    },
    setGroupingRowsInd() {
      this.tableData.forEach((item, i) => {
        if (item.grouping_row) this.groupingRowsInd.push(i);
      });
    },
    createGroupingData(data, key, groupKey) {
      let newData = [];
      let groupItemKey =
        groupKey === 'group' || this.groupNameKey ? groupKey : '';
      data.forEach((item, i, arr) => {
        if (!arr[i - 1] || item[groupKey] !== arr[i - 1][groupKey]) {
          newData.push({
            [key]: groupItemKey ? item[groupItemKey] : item[key],
            [key + '_colspan']:
              groupKey === 'GROUP' ? 1 : this.headersFlat.length,
            [key + '_grouping']: true,
            grouping_row: true,
            fixedColumn: item.GROUP,
            GROUP: item.GROUP,
            KOL_SEK_colspan: item.GROUP ? this.headersFlat.length - 1 : 0
          });
        }
        newData.push(item);
      });
      if (this.groupSort) {
        let groupSortKey = this.groupSort;
        newData.sort(function (a, b) {
          if (a[groupSortKey] > b[groupSortKey]) {
            return 1;
          }
          if (a[groupSortKey] < b[groupSortKey]) {
            return -1;
          }
          return 0;
        });
      }
      return newData;
    },
    setCollapse(i) {
      let groupRowInd = Math.max(
        ...this.groupingRowsInd.filter((el) => el < i)
      );
      return this.groupingRowsIndClose.some((el) => el === groupRowInd);
    },
    checkRowspanInd(i) {
      return Math.max(...this.rowspanRows.filter((el) => el < i));
    },
    setColspanRowColor() {
      this.tableData.forEach((item, i) => {
        let rowInd;
        if (Object.keys(item).some((el) => el.indexOf('_rowspan') >= 0)) {
          for (let key in item) {
            if (key.indexOf('_rowspan') >= 0) {
              if (item[key]) rowInd = i;
            }
          }
        }
        if (rowInd || rowInd === 0) this.rowspanRows.push(rowInd);
      });
    }
  },
  props: [
    'headers',
    'url',
    'params',
    'tableType',
    'isDetail',
    'hasDetail',
    'hasInfo',
    'isRemote',
    'groupSort',
    'groupKey',
    'groupNameKey'
  ],
  data() {
    return {
      progress: false,
      selectedCheckbox: [],
      editedValues: [],
      selectedItem: null,
      groupingRowsIndClose: [],
      groupingRowsInd: [],
      rowspanRows: [],
      cellClasses: {},
      selectedCell: {
        i: null,
        dataIndex: null
      },
      windowSize: {
        x: 0,
        y: 0
      },
      tableData: [],
      headersRemote: [],
      headersFlat: [],
      headerRows: [],
      headerDeep: 1
    };
  }
};
</script>

