//Планирование. Наряд-задание ЛБ
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'На ж/д сутки' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: 'Район управления ДРЛ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasDetail="true"
        @loadDetail="setDetail"
      />
      <Buttons :params="params" />
    </div>
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'PlanningIndicators',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons,
    DetailPanel
  },
  created() {
    this.$store.state.detailInfo.detailName = 'Детализация';
  },
  computed: {
    getDate() {
      const [year, month, day] = this.$store.state.filtersValues.date.split(
        '-'
      );
      return `${day}.${month}.${year}`;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: this.$store.state.filtersValues.drl
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/work_station_detail.json'
          : '/arm-isujt' + '/server/work_station_detail.json'
      ); //пока тестовые данные
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/plan_indicators.json',
      tableType: 'lc_task',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 3,
      detailHeaders: [],
      headers: [
        {
          text: 'Район управления',
          alignHeader: 'center',
          value: 'ind_name',
          width: '200',
          border: true
        },
        {
          text: 'Наряд-задание\n(ОУЭР)',
          value: 'ind_vol',
          border: true,
          alignHeader: 'center',
          width: '200',
          isBigText: true
        },
        {
          text: 'Обеспечение\nнаряд-задание (УТХ)',
          value: 'ind_uth',
          border: true,
          alignHeader: 'center',
          width: '200',
          isBigText: true
        }
      ]
    };
  }
};
</script>
