<template>
  <div class="mr-5">
    <p class="filter-label">Тип приказа</p>
    <v-menu
      v-model="treeView"
      transition="slide-y-transition"
      offset-y
      max-width="290px"
      min-width="420px"
      :close-on-content-click="false"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          v-on="on"
          :value="inputValue"
          solo
          flat
          hide-details
          readonly
          @click:append="showTree"
        >
          <template v-slot:append>
            <v-icon
              class="drop-icon"
              :value="value"
              :class="{ 'drop-icon-revert': treeView }"
              @click="showTree"
              >mdi-menu-down</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-treeview
        v-model="value"
        class="select-tree"
        open-all
        open-on-click
        selectable
        return-object
        activatable="true"
        :items="items"
        selected-color="#00194D"
        @input="setValue(value)"
      ></v-treeview>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'SelectTreeFilter',
  methods: {
    showTree() {
      this.treeView = !this.treeView;
    },
    setValue(value) {
      let valArr = value;
      if (valArr.length > 0) {
        let inputString = '';
        valArr.forEach((v, i) => {
          if (i === 0) {
            inputString += v.name;
          } else {
            inputString += ', ' + v.name;
          }
        });
        this.inputValue = inputString;
      } else this.inputValue = '';
    }
  },
  data: () => ({
    value: [],
    inputValue: '',
    treeView: false,
    items: [
      {
        id: 'Нормирование',
        name: 'Нормирование',
        children: [
          { id: 'ed', name: 'Электровозы по депо' },
          { id: 'td', name: 'Тепловозы по депо' },
          { id: 'ey', name: 'Электровозы по участкам' },
          { id: 'ty', name: 'Тепловозы по участкам' }
        ]
      },
      {
        id: 'Регулирование',
        name: 'Регулирование',
        children: [
          { id: 'regulation', name: 'Передислокация по стыковым станциям' }
        ]
      },
      {
        id: 'Планирование',
        name: 'Планирование',
        children: [
          { id: 'lb_depo', name: 'Заставка локомотивных бригад' },
          { id: 'lb_pas', name: 'Следование ЛБ пассажиром' }
        ]
      }
    ]
  })
};
</script>

