<template>
  <div class="mr-5">
    <p class="filter-label">{{ labelText }}</p>
    <v-menu
      v-model="fromDateMenu"
      class="mt-2"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          solo
          flat
          hide-details
          readonly
          :value="fromDateDisp"
          v-on="on"
          @click:append="showC"
        >
          <template v-slot:append>
            <v-icon color="#FF7141" class="cal-icon" @click="showC"
              >mdi-calendar</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-date-picker
        v-model="value"
        color="#4D6C99"
        header-color="#4D6C99"
        elevation="3"
        show-adjacent-months
        :weekday-format="getDay"
        @input="[setValueInStore(value)]"
        first-day-of-week="1"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'DateFilter',
  props: ['labelText', 'filterName'],
  data: (vm) => ({
    isShow: false,
    value: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    fromDateMenu: false
  }),
  created() {
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  computed: {
    fromDateDisp() {
      return this.formatDate(this.value);
    }
  },
  methods: {
    getDay(date) {
      const daysOfWeek = ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'];
      let i = new Date(date).getDay(date);
      return daysOfWeek[i];
    },
    setValueInStore(val) {
      this.$store.state.filtersValues.date = val;
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    showC() {
      this.fromDateMenu = !this.fromDateMenu;
    },
    closePicker() {
      this.fromDateMenu = false;
    }
  }
};
</script>

