            <v-col class="pr-0 pl-0">
              <v-row dense class="filter-block">
                <SelectFilter
                  ref="filter-seria"
                  isRemote="true"
                  :filterUrl="
                    this.$store.state.isTestMode
                      ? '../..' + '/server/filters_seria.json'
                      : '/arm-isujt' + '/server/filters_seria.json'
                  "
                  labelText="Серия"
                  keyText="name"
                  keyValue="id"
                  filterName="filter-seria"
                  inputWidth="120px"
                />
                <TextfieldFilter labelText="Номер" />
                <SelectFilter
                  :defaultValue="defaultValueRoad"
                  ref="filter-road-registry"
                  keyText="name"
                  keyValue="id"
                  :localItems="[
                    { id: 9991, name: 'ВП' },
                    { id: 88, name: 'КРЖД' },
                    { id: 92, name: 'ВСЖД' },
                    { id: 94, name: 'ЗБЖД' },
                    { id: 96, name: 'ДВЖД' }
                  ]"
                  labelText="Дорога приписки"
                  filterName="filter-road-registry"
                  inputWidth="190px"
                />
                <SelectFilter
                  ref="filter-depo-prip"
                  :isRemote="true"
                  :filterUrl="
                    this.$store.state.isTestMode
                      ? '../..' + '/server/filters_depot.json'
                      : '/arm-isujt' + '/server/filters_depot.json'
                  "
