        <DataTable
          :headers="headers_0"
          :url="getUrl_0"
          :params="{
            date: Math.floor(Date.now() / 1000),
            time_rep_2: this.$store.state.filtersValues['time_rep_2']
          }"
          ref="table_0"
          tableType="rep_control_0"
        />
      </v-col>
      <v-col class="col-6">
        <DataTable
          :headers="headers_1"
          :url="getUrl_1"
          :params="{
            date: Math.floor(Date.now() / 1000),
            time_rep_2: this.$store.state.filtersValues['time_rep_2']
          }"
          ref="table_1"
          tableType="rep_control_1"
          @forceUpdate="forceUpdateFirstTable()"
        />
      </v-col>
