<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import DateFilter from '../../components/inputs/DateFilter';
import SelectFilter from '../../components/inputs/SelectFilter';
import FilterButtons from '../../components/buttons/FilterButtons';
import TextfieldFilter from '../../components/inputs/TextfieldFilter';
import DataTable from '../../components/DataTable';
export default {
  name: 'LocoControl_02',
  components: {
    TextfieldFilter,
    Links,
    Title,
    DateFilter,
    SelectFilter,
    FilterButtons,
    DataTable
  },
  methods: {
    forceUpdateFirstTable() {
      this.$refs.table_0.tableData = this.$store.state.tableStores[
        'rep_control_0'
      ];
      this.$refs.table_0.setClasses();
      this.$forceUpdate();
    },
    reloadTable_0() {
      this.$refs.table_0.fetchTableData({
        drl: this.$store.state.filtersValues.drl
      });
    },
    reloadTable_1() {
      this.$refs.table_1.fetchTableData({
        drl: this.$store.state.filtersValues.drl
      });
    },
    resetAll() {
      for (let filter in this.$refs) {
        if (
          this.$store.state.defaultFiltersValues &&
          this.$store.state.defaultFiltersValues[filter]
        ) {
          if (filter === 'date')
            this.$store.state.defaultFiltersValues[
              filter
            ] = new Date().toISOString().substr(0, 10);

          this.$refs[filter][0].value = this.$store.state.defaultFiltersValues[
            filter
          ];
        }
      }
    },
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    }
  },
  created() {
    this.$store.state.filterOpen = true;
  },
  computed: {
    getUrl_0() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl_0
        : '/arm-isujt' + this.defaultUrl_0;
    },
    getUrl_1() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl_1
        : '/arm-isujt' + this.defaultUrl_1;
    }
  },
  data() {
    return {
      params: {
        buttons: {
          add: true,
          back: true
        },
        backUrl: '/report/main',
        name: 'Отчеты: локомотивы выделенного контроля',
        icon: 'icon icon-reports'
      },
      panel: 0,
      defaultUrl_0: '/server/fakeReports_2_test.json',
      defaultUrl_1: '/server/fakeReports_2_test2.json',
      defaultValueRoad: 9991,
      headers_0: [
        {
          text: '№',
          textMinify: 'mini',
          alignHeader: 'center',
          value: 'row_index'
        },
        {
          text: 'Серия',
          alignHeader: 'center',
          value: 'ser_name'
        },
        {
          text: 'Номер',
          alignHeader: 'center',
          value: 'num'
        },
        {
          text: 'Депо\nприписки',
          alignHeader: 'center',
          value: 'depo_prip'
        },
        {
          text: 'Основ.\nвид движ.',
          alignHeader: 'center',
          value: 'primary_vd_name'
        },
        {
          text: 'Текущ.\nвид движ.',
          alignHeader: 'center',
          value: 'actual_vd_name'
        },
        {
          text: 'Дата\nначала',
          alignHeader: 'center',
          isBigText: true,
          value: 'date_start'
        },
        {
          text: 'Дата\nокончания',
          alignHeader: 'center',
          editable: true,
          editType: 'date',
          value: 'date_end'
        }
      ],
      headers_1: [
        {
          text: '№',
          textMinify: 'mini',
          alignHeader: 'center',
          value: 'row_index'
        },
        {
          text: 'Серия',
          alignHeader: 'center',
          value: 'ser_name'
        },
        {
          text: 'Номер',
          alignHeader: 'center',
          value: 'num'
        },
        {
          text: 'Депо\nприписки',
          alignHeader: 'center',
          value: 'depo_prip'
        },
        {
          text: 'Основ.\nвид движ.',
          alignHeader: 'center',
          value: 'primary_vd_name'
        },
        {
          text: 'Текущ.\nвид движ.',
          alignHeader: 'center',
          value: 'actual_vd_name'
        },
        {
          text: 'Дата\nначала',
          alignHeader: 'center',
          editable: true,
          width: 100,
          editType: 'date',
          value: 'date_start'
        },
        {
          text: 'Добавить',
          value: 'add',
          editType: 'button',
          alignHeader: 'center'
        }
      ]
    };
  }
};
</script>

