          width: '250',
          textMinify: 'mini',
          value: 'section_id',
          rightBorder: true,
          fixedColumn: true
        },
        {
          text: 'Время начала\nдействия показателя',
          align: 'start',
          value: 'start_date',
          textMinify: 'mini'
        },
        {
