          textMinify: 'mini',
          editable: true
        },
        {
          text: 'Графиковое количество\nгрузовых поездов',
          align: 'start',
          value: 'freight_train',
          textMinify: 'mini',
          editable: true
        },
        {
          text: 'Плановое количество\nпассажирских поездов\nчет',
          align: 'start',
          value: 'train_count_even',
