//Планирование. Показатели плана
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        {
          filterName: 'version',
          filterType: 'select',
          labelText: 'Версия',
          inputWidth: '260px'
        },
        {
          filterName: 'road',
          filterType: 'select',
          labelText: 'Дороги',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/road_filter.json'
            : '/arm-isujt' + '/server/road_filter.json',
          keyText: 'name',
          keyValue: 'id'
        },
        {
          filterName: 'range_date',
          filterType: 'range_date',
          labelText: 'За период'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{
          version: Math.floor(Date.now() / 1000),
          road: 0,
          date: Math.floor(Date.now() / 1000)
        }"
        ref="table"
        :tableType="tableType"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';

export default {
  name: 'PlanningStatistics',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  created() {
    this.$store.state.detailInfo.detailName = 'Детализация';
  },
  computed: {
    getDate() {
      const [year, month, day] = this.$store.state.filtersValues.date.split(
        '-'
      );
      return `${day}.${month}.${year}`;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        version: this.getFormattedDate(this.$store.state.filtersValues.date),
        road: this.$store.state.filtersValues.road
      });
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/statistic.json',
      tableType: 'plan_ind',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 3,
      headers: [
        {
          text: '№',
          value: 'row_number',
          textMinify: 'mini',
          isBigText: true,
          width: '50',
          border: true
        },
        {
          text: 'Критерий',
          value: 'description',
          border: true,
          width: '250',
          alignHeader: 'center'
        },
        {
          text: 'Ед.',
          value: 'units',
          border: true,
          width: '100',
          alignHeader: 'center'
        },
        {
          text: 'Значение',
          value: 'value',
          width: '100',
          border: true,
          alignHeader: 'center'
        }
      ]
    };
  }
};
</script>
