//Отчеты. По плечам обслуживания
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'range_date', labelText: 'Интервал' },
        {
          filterName: 'report-19-trac',
          filterType: 'select',
          inputWidth: '160px',
          labelText: 'Вид тяги',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          defaultValue: 0,
          filterData: [
            { id: 0, name: 'Все' },
            { id: 1, name: 'Электровозы' },
            { id: 2, name: 'Тепловозы' }
          ]
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle" v-if="tableTitle">
      Отчет 19: сходы по плечам обслуживания ()
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
    </div>
    <DataTable
      :headers="headers"
      :url="getUrl"
      :params="{ date: Math.floor(Date.now() / 1000) }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import DataTable from '../../components/DataTable';
export default {
  name: 'Report19_2',
  components: {
    Links,
    Title,
    Filters,
    DataTable
  },
  methods: {
    updateTable() {
      if (this.$store.state.filtersValues['date'].length > 0) {
        const [year, month, day] = this.$store.state.filtersValues[
          'date'
        ][0].split('-');
        const [year2, month2, day2] = this.$store.state.filtersValues[
          'date'
        ][1].split('-');
        this.timeTitle = `с ${day}.${month}.${year} 18:00 по ${day2}.${month2}.${year2} 18:00`;
      } else this.timeTitle = '';
      this.tableTitle = true;
      this.$refs.table.fetchTableData({
        drl: this.$store.state.filtersValues.drl
      });
    }
  },
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      tableTitle: false,
      timeTitle: '',
      params: {
        buttons: {
          add: true,
          back: true
        },
        backUrl: '/report/main',
        name: 'Отчеты: по плечам обслуживания',
        icon: 'icon icon-reports'
      },
      defaultUrl: '/server/fakeReports_19_2.json',
      tableType: 'rep_19_2',
      headers: [
        {
          text: 'Общее',
          alignHeader: 'center',
          children: [
            {
              text: '№\nп/п',
              alignHeader: 'center',
              value: 'row_number'
            },
            {
              text: 'УОЛ',
              width: 150,
              alignHeader: 'center',
              value: 'uol_name'
            },
            {
              text: 'Станция отцепки локомотивов',
              alignHeader: 'center',
              value: 'st_name'
            },
            {
              text: 'Всего\nслучаев',
              alignHeader: 'center',
              value: 'cta'
            }
          ]
        },
        {
          text: 'в т.ч. с разбивкой по причинам',
          alignHeader: 'center',
          children: [
            {
              text: 'ТО-2',
              alignHeader: 'center',
              value: 'p_to2'
            },
            {
              text: 'Экипировка',
              alignHeader: 'center',
              value: 'p_ekpr'
            },
            {
              text: 'Неплановый\nремонт',
              alignHeader: 'center',
              value: 'p_mpr'
            },
            {
              text: 'Отказ от\nприемки бригадой',
              alignHeader: 'center',
              value: 'p_otb'
            },
            {
              text: 'Регулировка',
              alignHeader: 'center',
              value: 'p_reg'
            },
            {
              text: 'Без определения\nпричины',
              alignHeader: 'center',
              value: 'p_bpr'
            }
          ]
        }
      ]
    };
  }
};
</script>

