//Приказы
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        {
          filterName: 'range_date',
          filterType: 'range_date',
          labelText: 'За период'
        },
        {
          filterType: 'select_tree'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../components/Title';
import Links from '../components/buttons/Links';
import Filters from '../components/Filters';
import DataTable from '../components/DataTable';
import Buttons from '../components/buttons/Buttons';

export default {
  name: 'Order',
  components: {
    Title,
    Links,
    Filters,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/orders.json',
      tableType: 'order',
      params: {
        buttons: {
          print: true
        },
        name: 'Журнал приказов',
        icon: 'icon icon-list-alt'
      },
      headers: [
        {
          text: 'Номер',
          align: 'start',
          alignHeader: 'center',
          value: 'number'
        },
        {
          text: 'Дата',
          align: 'start',
          width: '110',
          alignHeader: 'center',
          value: 'dateFor'
        },
        {
          text: 'Тип приказа',
          align: 'start',
          width: '400',
          value: 'type',
          alignHeader: 'center'
        },
        {
          text: 'Согласовал',
          align: 'start',
          value: 'agreed',
          alignHeader: 'center'
        },
        {
          text: 'Утвердил',
          align: 'start',
          value: 'approved',
          alignHeader: 'center'
        },
        {
          text: 'Адресаты',
          align: 'start',
          width: '320',
          value: 'recipient',
          alignHeader: 'center'
        }
      ]
    };
  }
};
</script>
