//Нормирование. Участки
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :exportUrl="createExportUrl" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: 'Район управления ДРЛ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
        :hasDetail="true"
        @loadDetail="setDetail"
      />
      <Buttons :params="params" />
    </div>
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'NormalizationSections',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons,
    DetailPanel
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      params: {
        buttons: {
          print: true,
          export: true,
          recalc: true,
          save: true,
          cancel: true
        },
        name: 'Оперативное нормирование',
        icon: 'icon icon-trending-up'
      },
      requestForExport: 'method=norm_1&tab=panel-reportOP',
      defaultUrl: '/server/norm_1.json',
      detailHeaders: [
        {
          text: '№ п/п',
          value: 'row_number',
          alignHeader: 'center',
          border: true
        },
        {
          text: 'Время\nначала «Окна»',
          value: 'train_num',
          border: true
        },
        {
          text: 'Время\nокончания «Окна»',
          value: 'train_ind',
          border: true
        },
        {
          text: 'Продолжительность\n«Окна» (час)',
          value: 'short_oper',
          border: true
        },
        {
          text: 'Описание места\nпроведения (перегон)',
          value: 'time_oper',
          border: true
        },
        {
          text: 'Описание\nвыполняемых работ',
          value: 'location',
          border: true
        }
      ],
      tabModel: 0,
      tableType: 'norm_sec',
      headers: [
        {
          text: 'Тяговый полигон',
          align: 'start',
          textMinify: 'mini',
          fixedColumn: true,
          children: [
            {
              text: 'Наименование\nрасчетных участков',
              value: 'long_name',
              textMinify: 'mini',
              width: '200',
              border: true,
              rightBorder: true,
              fixedColumn: true
            }
          ]
        },
        { text: 'Длина\n(L,км)', value: 'distance', border: true },
        { text: 'Коэф.\nКп.гр.э', value: 'k_pgre', border: true },
        { text: 'Коэф.\nКп.п.э.', value: 'k_pe', border: true },
        {
          text: 'Кол-во\nпоездов\nN(max)',
          value: 'tr_num',
          border: true,
          editable: true,
          tooltip: true
        },
        {
          text: 'В т.ч. тяжело\nвесных N\n(2-ая тяга\nСМЕТ)',
          value: 'tr_heavy_num',
          border: true
        },
        { text: 'Окна,\nчас', value: 'duration', border: true },
        {
          text: 'Потребность локомотивов',
          value: 'way',
          cols: 15,
          alignHeader: 'center',
          children: [
            {
              text: 'Грузовое движение',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 'ft', border: true },
                { text: 'Мт\n(3-х)', value: 'ft3', border: true },
                { text: 'Мт\n(2-х)', value: 'fl', border: true }
              ]
            },
            {
              text: 'ПВД',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(3-х)',
                  value: 'locl3',
                  border: true,
                  editable: true
                },
                {
                  text: 'Мт\n(2-х)',
                  value: 'locl',
                  border: true,
                  editable: true
                }
              ]
            },
            {
              text: 'ТОЛКАЧИ',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(4-х)',
                  value: 'pt4',
                  border: true,
                  editable: true
                },
                {
                  text: 'Мт\n(3-х)',
                  value: 'pt',
                  border: true,
                  editable: true
                },
                { text: 'Мт\n(2-х)', value: 'pl', border: true, editable: true }
              ]
            },
            {
              text: 'ОПЕР, РД',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(4-х)',
                  value: 'rt4',
                  border: true,
                  editable: true
                },
                {
                  text: 'Мт\n(3-х)',
                  value: 'rt',
                  border: true,
                  editable: true
                },
                { text: 'Мт\n(2-х)', value: 'rl', border: true, editable: true }
              ]
            },
            {
              text: 'ВСЕГО',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 't', border: true },
                { text: 'Мт\n(3-х)', value: 't3', border: true },
                { text: 'Мт\n(2-х)', value: 'l', border: true }
              ]
            },
            {
              text: 'ИТОГО',
              alignHeader: 'center',
              children: [{ text: 'Мв', value: 'total', border: true }]
            }
          ]
        }
      ]
    };
  },
  created() {
    this.$store.state.detailInfo.detailName =
      'Планируемые окна на расчетном участке';
  },
  computed: {
    createExportUrl() {
      return (
        'method=norm_1&tab=panel-reportOP&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: this.$store.state.filtersValues.drl
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/analize_details.json'
          : '/arm-isujt' + '/server/analize_details.json'
      ); //пока тестовые данные
    }
  }
};
</script>
