//Отчеты. ТП - Нормирование
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        {
          filterName: 'date',
          filterType: 'range_date',
          labelText: '\nВыберите период'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle" v-if="timeTitle">
      Справка о выполнении технологического процесса на период с
      <span class="page-subtitle-bold">{{ timeTitle }}</span>
    </div>
    <DataTable
      :headers="headers"
      :url="getUrl"
      :params="{ date: Math.floor(Date.now() / 1000) }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
