import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import DataTable from '../../components/DataTable';
export default {
  name: 'Report_tp_norm',
  components: {
    Links,
    Title,
    Filters,
    DataTable
  },
  data() {
    return {
      params: {
        buttons: {
          add: true,
          back: true
        },
        backUrl: '/report/main',
        name: 'Отчеты: ТП - Нормирование',
        icon: 'icon icon-reports'
      },
      timeTitle: '',
      tableType: 'rep_tp_norm',
      defaultUrl: '/server/fakeReports_norm.json',
      headers: [
        {
          text: 'Дата контроля',
          alignHeader: 'center',
          border: true,
          value: 'control_date'
        },
        {
          text: 'Нормирование на дату',
          alignHeader: 'center',
          value: 'normalization_date',
          border: true
        },
        {
          text: 'АС ССП по техническим станциям',
          alignHeader: 'center',
          children: [
            {
              text: 'КРАС',
              alignHeader: 'center',
              value: 'road_kras'
            },
            {
              text: 'ВСИБ',
              alignHeader: 'center',
              value: 'road_vsib'
            },
            {
              text: 'ЗАБ',
              alignHeader: 'center',
              value: 'road_zab'
            },
            {
              text: 'Д-ВОСТ',
              alignHeader: 'center',
              value: 'road_dvost'
            }
          ]
        },
        {
          text: 'ДРЛ',
          alignHeader: 'center',
          children: [
            {
              text: '1',
              alignHeader: 'center',
              value: 'drl_1'
            },
            {
              text: '2',
              alignHeader: 'center',
              value: 'drl_2'
            },
            {
              text: '3',
              alignHeader: 'center',
              value: 'drl_3'
            },
            {
              text: '4',
              alignHeader: 'center',
              value: 'drl_4'
            }
          ]
        },
        {
          text: 'СДРЛ',
          alignHeader: 'center',
          children: [
            {
              text: '1',
              alignHeader: 'center',
              value: 'sdrl_1'
            },
            {
              text: '2',
              alignHeader: 'center',
              value: 'sdrl_2'
            },
            {
              text: '3',
              alignHeader: 'center',
              value: 'sdrl_3'
            },
            {
              text: '4',
