            }
          ]
        },
        {
          text: 'Утверждение нач. ЦУП',
          alignHeader: 'center',
          value: 'cutr',
          border: true
        }
      ]
    };
  },
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    updateTable() {
      if (this.$store.state.filtersValues['date'].length > 0) {
        const [year, month, day] = this.$store.state.filtersValues[ 'date' ][0].split('-');
        const [year2, month2, day2] = this.$store.state.filtersValues[ 'date' ][1].split('-');
        this.timeTitle = `${day}.${month}.${year} по ${day2}.${month2}.${year2}`;
      } else this.timeTitle = '';
      this.tableTitle = true;
      this.$refs.table.fetchTableData({
        drl: this.$store.state.filtersValues.drl
      });
    }
  }
};
</script>

