<template>
  <div>
    <p class="filter-label">{{ labelText }}</p>
    <v-select
      v-model="value"
      :items="items"
      color="#FF7141"
      :item-text="`${keyText}`"
      :item-value="`${keyValue}`"
      :style="{
        width: inputWidth
      }"
      solo
      flat
      hide-details
      @change="changeValue()"
    >
      <template #item="{ item }">
        <span class="select-item-text">{{ item[`${keyText}`] }}</span>
      </template>
    </v-select>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SelectFilter',
  created() {
    this.setModelValue();
    if (this.isRemote) {
      this.fetchItems();
    } else {
      if (this.filterName === 'time_rep_2') {
        this.items = this.$store.state.timeFilterCurrentValue;
      } else this.items = this.localItems;
    }
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  methods: {
    fetchItems() {
      axios.get(this.filterUrl).then((response) => {
        this.items = response.data.data;
      });
    },
    changeValue() {
      this.$store.state.filtersValues[this.filterName] = this.value;
      if (this.filterName === 'type_rep_2') {
        switch (this.value) {
          case 0: //срезовой
            this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[0];
            break;
          case 1: //моментальный
            this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[1];
            break;
        }
      }
    },
    setModelValue() {
      this.value = this.defaultValue ? this.defaultValue : this.value;
    }
  },
  props: [
    'labelText',
    'filterName',
    'isRemote',
    'filterUrl',
    'localItems',
    'keyText',
    'keyValue',
    'inputWidth',
    'defaultValue'
  ],
  data() {
    return {
      value: 0,
      items: []
    };
  }
};
</script>

