<template>
  <div class="flex mt-1">
    <v-checkbox
      v-model="checked"
      color="#00194D"
      hide-details
      :ripple="false"
      :label="labelText"
      :disabled="this.$store.state.dependingCheckboxes[this.filterName]"
      @click="changeValue()"
    ></v-checkbox>
  </div>
</template>

<script>
export default {
  name: 'FilterCheckbox',
  props: ['labelText', 'filterName', 'disableFilterName'],
  created() {
    this.checked = this.$store.state.filtersValues[this.filterName] = false;
  },
  methods: {
    changeValue() {
      this.$store.state.filtersValues[this.filterName] = this.checked;
      this.$store.state.dependingCheckboxes[
        this.disableFilterName
      ] = this.checked;
    }
  },
  data() {
    return {
      checked: false
    };
  }
};
</script>

<style>
.theme--light.v-icon {
  color: #00194d;
}
.v-input .v-label {
  font-size: 14px;
  color: #00194d;
  margin-left: -5px;
  margin-right: 5px;
  margin-top: 2px;
}
</style>
