<template>
  <v-card
    v-resize="onResize"
    id="table"
    class="table"
    elevation="0"
    :class="{
      'slide-leave slide-enter-active': this.$store.state.sheet && hasDetail,
      'slide-leave-to slide-leave-active': !this.$store.state.sheet && hasDetail
    }"
    :style="getTableHeight"
  >
    <div class="graf">
      <div id="planTime"><svg></svg></div>
      <div class="graf-container">
        <div id="planNumber"><svg></svg></div>
        <div id="locoPlan"><svg></svg></div>
      </div>
    </div>
  </v-card>
</template>

<script>
import axios from 'axios';
import * as d3Selection from 'd3-selection';

const d3 = Object.assign({}, d3Selection);

export default {
  name: 'GrafTable',
  props: ['hasDetail', 'url'],
  data() {
    return {
      windowSize: {
        x: 0,
        y: 0
      }
    };
  },
  created() {
    this.fetchData();
  },
  computed: {
    getTableHeight() {
      const FILTERS_CONTENT_HEIGHT = 138,
        BLOCKS_HEIGHT = 276,
        DETAIL_HEIGHT = 65,
        OPEN_DETAIL_CONTENT = 190;
      let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

      if (this.hasDetail) tableHeight -= DETAIL_HEIGHT;

      if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
      if (this.$store.state.sheet) tableHeight -= OPEN_DETAIL_CONTENT;

      return `height: ${tableHeight}px`;
    }
  },
  methods: {
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    fetchData() {
      axios.get(this.url).then((response) => {
        this.drawReverseListGraph('#locoPlan', 'loco', response.data);
      });
    },
    convertStringToDate(fullDate) {
      const time = fullDate.split(' ')[0];
      const date = fullDate.split(' ')[1];

      const hour = +time.split(':')[0];
      const minute = +time.split(':')[1];

      const day = +date.split('.')[0];
      const month = +date.split('.')[1] - 1;
      const year = +date.split('.')[2];

      return new Date(year, month, day, hour, minute);
    },
    drawReverseListGraph: function (selector, type, data) {
      let me = this;

      Date.prototype.format = function (format) {
        const o = {
          'M+': this.getMonth() + 1, //month
          'd+': this.getDate(), //day
          'h+': this.getHours(), //hour
          'm+': this.getMinutes(), //minute
          's+': this.getSeconds(), //second
          'q+': Math.floor((this.getMonth() + 3) / 3), //quarter
          S: this.getMilliseconds() //millisecond
        };

        if (/(y+)/.test(format))
          format = format.replace(
            RegExp.$1,
            (this.getFullYear() + '').substr(4 - RegExp.$1.length)
          );
        for (let k in o)
          if (new RegExp('(' + k + ')').test(format))
            format = format.replace(
              RegExp.$1,
              RegExp.$1.length === 1
                ? o[k]
                : ('00' + o[k]).substr(('' + o[k]).length)
            );
        return format;
      };

      d3.select(selector).selectAll('svg').remove();
      d3.select('#planNumber').selectAll('svg').remove();
      d3.select('#planTime').selectAll('svg').remove();

      if (data.data && data.data.length !== 0) {
        const style_loco = 'fill: #00194D; font: 14px FSRAILBold;';
        const style_text = 'fill: #00194D; font: 10px FSRAILBook;';
        const style_time = 'font: 14px FSRAILBold; fill: #7387A0;';

        const x = data.timeLine.length * 150;
        const y = data.totalCount * 50;

        let svgNumber = d3
          .select('#planNumber')
          .append('svg')
          .attr('width', 150)
          .attr('height', y + 100);

        let svgTime = d3
          .select('#planTime')
          .append('svg')
          .attr('width', x)
          .attr('height', 50);

        let svg = d3
          .select(selector)
          .append('svg')
          .attr('width', x)
          .attr('height', y + 100);

        const width = parseInt(svg.style('width'));
        const startX = 100;
        const startY = 70;
        const step = y / data.totalCount;

        let timeLine = {};
        let timeLineStep = (width - 50) / data.timeLine.length;
        // Время планирования
        svgTime
          .append('rect')
          .attr('fill', 'white')
          .attr('style', 'box-shadow: 10px 10px grey;')
          .attr('x', 0)
          .attr('y', 0)
          .attr('width', x)
          .attr('height', 50);

        let minuteInterval =
          (this.convertStringToDate(data.timeLine[1]) -
            this.convertStringToDate(data.timeLine[0])) /
          (1000 * 60);

        let fullTimeLine = [];

        data.timeLine.map(function (d, i) {
          if (d) {
            let time = d.split(' ')[0],
              date = d.split(' ')[1];
            svgTime
              .append('text')
              .attr('style', style_time)
              .attr('text-anchor', 'middle')
              .attr('x', 85 + i * timeLineStep)
              .attr('y', startY - 45)
              .text(time);
            svgTime
              .append('text')
              .attr('style', style_time)
              .attr('text-anchor', 'middle')
              .attr('x', 85 + i * timeLineStep)
              .attr('y', startY - 30)
              .text(date.split('.')[0] + '.' + date.split('.')[1]);
            svg
              .append('line')
              .attr('stroke', '#D2DAE7')
              .attr('stroke-dasharray', '4')
              .attr('stroke-width', '1.5')
              .attr('x1', startX + 50 + i * timeLineStep)
              .attr('y1', startY - 15)
              .attr('x2', startX + 50 + i * timeLineStep)
              .attr('y2', y + 15);

            for (let i = 0; i < minuteInterval; i++) {
              let thisTime = me.convertStringToDate(d);
              fullTimeLine.push(
                new Date(thisTime.setMinutes(thisTime.getMinutes() + i)).format(
                  'hh:mm dd.MM.yyyy'
                )
              );
            }
          }
        });

        fullTimeLine.map(function (d, i) {
          timeLine[d] = startX + 50 + (i * timeLineStep) / minuteInterval;
        });

        svgNumber
          .append('rect')
          .attr({ fill: 'white', x: 0, y: 0, width: 110, height: y + 100 });
        data.data.map(function (d, i) {
          if (d.number) {
            if (d.series)
              svgNumber
                .append('text')
                .attr('style', style_loco)
                .attr('text-anchor', 'start')
                .attr('x', startX - 90)
                .attr('y', startY + i * step)
                .text(d.series);
            // Номер локомотива
            svgNumber
              .append('text')
              .attr('style', style_loco)
              .attr('text-anchor', 'end')
              .attr('x', startX + 40)
              .attr('y', startY + i * step)
              .text(d.number);
            // Анализ состояний для локомотива
            d.states.map(function (q) {
              var drawStart = true;
              let drawEnd = true;
              let style;
              let styleColor;
              if (q.state === '0') {
                styleColor = '#2469FF';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; stroke-dasharray: 5; cursor: pointer;';
              } else if (q.state === '1') {
                styleColor = '#00CF00';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; cursor: pointer;';
              } else if (q.state === '8') {
                styleColor = 'black';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; stroke-dasharray: 5; cursor: pointer;';
              } else if (q.state === '4') {
                styleColor = '#FFAE00';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; stroke-dasharray: 5; cursor: pointer;';
              } else if (q.state === '9') {
                styleColor = 'red';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; stroke-dasharray: 5; cursor: pointer;';
              } else {
                styleColor = '#BD0DFF';
                style =
                  'stroke: ' +
                  styleColor +
                  '; stroke-width: 3; stroke-dasharray: 5; cursor: pointer;';
              }

              if (q.time_from && q.time_to) {
                let tooltipText =
                  q.station_from +
                  ' (' +
                  q.time_from.split(' ')[0] +
                  ')' +
                  ' - ' +
                  q.station_to +
                  ' (' +
                  q.time_to.split(' ')[0] +
                  ')';
                // Линия планирования
                let lineInfo = svg
                  .append('line')
                  .attr('style', style)
                  .attr('x1', timeLine[q.time_from])
                  .attr('y1', startY + i * step)
                  .attr('x2', timeLine[q.time_to])
                  .attr('y2', startY + i * step);
                lineInfo.on('mouseover', function () {
                  if (q.station_from && q.station_to) {
                    svg
                      .append('text')
                      .text(tooltipText)
                      .attr('class', 'tooltip')
                      .attr('style', style_text)
                      .attr('text-anchor', 'middle')
                      .attr(
                        'x',
                        timeLine[q.time_from] +
                          (timeLine[q.time_to] - timeLine[q.time_from]) / 2
                      )
                      .attr('y', startY + i * step + 30);
                  }
                });
                lineInfo.on('mouseout', function () {
                  svg.selectAll('.tooltip').remove();
                });
                if (timeLine[q.time_from]) {
                  if (q.station_from) {
                    // Станция откуда
                    if (drawStart) {
                      var station_from = svg
                        .append('circle')
                        .attr('style', 'cursor: pointer;')
                        .attr('fill', styleColor)
                        .attr('r', 5)
                        .attr('cx', timeLine[q.time_from] + 5)
                        .attr('cy', startY + i * step);
                      station_from.on('mouseover', function () {
                        svg
                          .append('text')
                          .text(tooltipText)
                          .attr('class', 'tooltip')
                          .attr('style', style_text)
                          .attr('text-anchor', 'middle')
                          .attr('x', timeLine[q.time_from])
                          .attr('y', startY + i * step + 30);
                      });
                      station_from.on('mouseout', function () {
                        svg.selectAll('.tooltip').remove();
                      });
                    }
                  }
                }

                if (timeLine[q.time_to]) {
                  if (q.station_to) {
                    // Станция куда
                    if (drawEnd) {
                      let station_to = svg
                        .append('circle')
                        .attr('style', 'cursor: pointer;')
                        .attr('fill', styleColor)
                        .attr('r', 5)
                        .attr('cx', timeLine[q.time_to] - 5)
                        .attr('cy', startY + i * step);
                      station_to.on('mouseover', function () {
                        svg
                          .append('text')
                          .text(tooltipText)
                          .attr('class', 'tooltip')
                          .attr('style', style_text)
                          .attr('text-anchor', 'middle')
                          .attr('x', timeLine[q.time_to])
                          .attr('y', startY + i * step + 30);
                      });
                      station_to.on('mouseout', function () {
                        svg.selectAll('.tooltip').remove();
                      });
                    }
                  }
                }

                // if (timeLine[q.time_from] && timeLine[q.time_to]) {
                //   if (q.number) {
                //     // Номер поезда
                //     if (q.number !== lastNumber)
                //       svg
                //         .append('text')
                //         .attr('style', style_text)
                //         .attr('text-anchor', 'middle')
                //         .attr(
                //           'x',
                //           timeLine[q.time_from] +
                //             (timeLine[q.time_to] - timeLine[q.time_from]) / 2
                //         )
                //         .attr('y', startY - 10 + i * step)
                //         .text(q.number);
                //     lastNumber = q.number;
                //   }
                // }
              }
            });
          }
        });
        svgNumber
          .append('text')
          .attr('style', style_time)
          .attr('text-anchor', 'start')
          .attr('x', startX - 85)
          .attr('y', y + 70)
          .text('Всего: ' + data.totalCount);
      }
    }
  }
};
</script>

<style scoped lang="sass">
#table
  overflow: auto

.v-sheet.v-card
  border-radius: 0 $common-radius 0 0

.graf-container
  display: inline-flex

#planNumber
  position: sticky
  left: 0
  margin-top: -50px
  background-color: #fff

#planTime
  position: sticky
  top: 0
  margin-left: 115px

#locoPlan
  margin-left: -110px
  margin-top: -50px
</style>
