//Отчеты. Технический план
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        {
          filterName: 'report-2-tech_datefield',
          filterType: 'date',
          labelText: 'Действуют с:'
        },
        {
          filterName: 'report-2-tech_timefield',
          filterType: 'select',
          inputWidth: '120px',
          labelText: 'Время',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          filterData: setTimeFilterData
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle" v-if="isTableTitle">
      {{ tableTitle }}
    </div>
    <template>
      <v-card
        v-resize="onResize"
        id="table"
        class="table pl-3"
        elevation="0"
        :class="{
          'slide-leave slide-enter-active': this.$store.state.sheet,
          'slide-leave-to slide-leave-active': !this.$store.state.sheet
        }"
        :style="getTableHeight"
      >
        <DynamicTable
          v-for="(item, i) in this.$store.state.techPlanReport.tableArr"
          :key="i"
          :tableData="item"
        />
        <v-progress-circular
          v-if="progress"
          class="progress-circle"
          indeterminate
          color="#00194D"
        ></v-progress-circular>
      </v-card>
    </template>
  </div>
</template>

<script>
import axios from 'axios';
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import DynamicTable from '../../components/DynamicTable';
export default {
  name: 'Report02_tech',
  components: {
    Links,
    Title,
    Filters,
    DynamicTable
  },
  methods: {
    getTableData() {
      this.progress = true;
      axios.get(this.getUrl).then((response) => {
        this.progress = false;
        this.$store.state.techPlanReport.tableArr = response.data.data;
        this.tableTitle = this.setFormattedTableTitle(response.data.alert);
      });
    },
    updateTable() {
      this.getTableData();
      this.isTableTitle = true;
      // this.$refs.table.fetchTableData({
      //   date: this.getFormattedDate(this.$store.state.filtersValues.date),
      //   drl: this.$store.state.filtersValues.drl
      // });
    },
    setFormattedTableTitle(alert) {
      let titleArr = alert.split('дату ');
      let dateArr = titleArr[1].split(' ');
      const [year, month, day] = dateArr[0].split('-');
      let formattedDate = `${day}.${month}.${year}`;
      return titleArr[0] + 'дату ' + formattedDate + ' ' + dateArr[1];
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    }
  },
  computed: {
    getTableHeight() {
      const FILTERS_CONTENT_HEIGHT = 138,
        BLOCKS_HEIGHT = 276;
      let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

      if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
      return `height: ${tableHeight}px`;
    },
    setTimeFilterData() {
      let timeArr = [];
      for (let i = 0; i < 24; i++) {
        timeArr.push({
          id: i,
          name: i < 10 ? '0' + i + ':00' : i + ':00'
        });
      }
      return timeArr;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      defaultUrl: '/server/fakeReports_02_02.json',
      progress: false,
      isTableTitle: false,
      tableTitle: '',
      params: {
        buttons: {
          add: true,
          back: true
        },
        backUrl: '/report/main',
        name: 'Отчеты: технический план',
        icon: 'icon icon-reports'
      },
      windowSize: {
        x: 0,
        y: 0
      }
    };
  }
};
</script>

<style scoped lang="sass">
#table
  display: flex
  overflow: auto
  div:last-child
    margin-right: 12px
.progress-circle
  position: absolute
  z-index: 99
  top: 50%
  left: 50%
</style>
