<template>
  <v-menu
    v-model="fromDateMenu"
    class="mt-2"
    transition="scale-transition"
    offset-y
    max-width="290px"
    min-width="290px"
  >
    <template v-slot:activator="{ on }">
      <v-text-field
        solo
        flat
        hide-details
        readonly
        :value="fromDateDisp"
        v-on="on"
        @click:append="showC"
      ></v-text-field>
    </template>
    <v-date-picker
      v-model="value"
      color="#4D6C99"
      header-color="#4D6C99"
      elevation="3"
      show-adjacent-months
      :weekday-format="getDay"
      @input="[setValueInStore(value)]"
      first-day-of-week="1"
    ></v-date-picker>
  </v-menu>
</template>

<script>
export default {
  name: 'EditCellDate',
  props: ['row', 'col', 'oldValue', 'rowId'],
  computed: {
    fromDateDisp() {
      let date = '-';
      if (this.value) {
        if (this.formatDateFromStore(this.value)) {
          date = this.formatDateFromStore(this.value);
        } else if (this.formatDate(this.value)) {
          date = this.formatDate(this.value);
        }
      }
      return date;
    }
  },
  created() {
    this.value = this.row[this.col.value];
  },
  data() {
    return {
      isShow: false,
      value: '',
      fromDateMenu: false
    };
  },
  methods: {
    getDay(date) {
      const daysOfWeek = ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'];
      let i = new Date(date).getDay(date);
      return daysOfWeek[i];
    },
    setValueInStore(value) {
      this.value = value;
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    formatDateFromStore(date) {
      const [year, month, day] = date.split(' ')[0].split('-');
      return `${day}.${month}.${year}`;
    },
    showC() {
      this.fromDateMenu = !this.fromDateMenu;
    }
  }
};
</script>

<style>
.v-input input {
  color: #00194d !important;
}

.theme--light.v-input input {
  color: #00194d !important;
}
.theme--light.v-input .v-input__slot {
  background: transparent !important;
}
</style>

