//Планирование. План по ЛБ/Сменно-суточный план содержания ЛБ
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'time',
          filterType: 'select',
          labelText: 'Время',
          inputWidth: '120px',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          filterData: [
            { id: 3, name: '6:00' },
            { id: 6, name: '8:00' },
            { id: 0, name: '10:00' },
            { id: 24, name: '12:00' }
          ]
        },
        {
          filterName: 'selected_depot',
          filterType: 'select',
          labelText: 'Выбранное депо',
          inputWidth: '260px',
          isRemote: false,
          keyText: 'name',
          keyValue: 'id',
          filterData: [{ id: 0, name: 'Все' }]
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle">
      Сменно-суточный план содержания локомотивных бригад с
      <span class="page-subtitle-bold">{{ getDate }} </span>
      по <span class="page-subtitle-bold">{{ getDate }} </span>
    </div>
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{
          date: Math.floor(Date.now() / 1000),
          time: 0,
          selected_depot: 0
        }"
        ref="table"
        :tableType="tableType"
        :hasDetail="true"
        :hasInfo="true"
        @loadDetail="setDetail"
      />
      <Buttons :params="params" />
    </div>
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'PlanningMidLocoCrew',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons,
    DetailPanel
  },
  created() {
    this.$store.state.detailInfo.detailName = 'Детализация';
  },
  computed: {
    getDate() {
      let d =
        this.$store.state.filtersValues.date ||
        new Date().toISOString().substr(0, 10);
      const [year, month, day] = d.split('-');
      return `${day}.${month}.${year}`;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        time: this.$store.state.filtersValues.time,
        selected_depot: this.$store.state.filtersValues.selected_depot
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/work_station_detail.json'
          : '/arm-isujt' + '/server/work_station_detail.json'
      ); //пока тестовые данные
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/get_input_train.json',
      tableType: 'lc_plan',
      params: {
        buttons: {
          print: true,
          titleTop: 30
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 4,
      detailHeaders: [],
      headers: [
        {
          text: 'Участок работы',
          alignHeader: 'center',
          value: 'train_index_tooltip',
          border: true
        },
        {
          text: 'Количество ЛБ',
          cols: 12,
          alignHeader: 'center',
          isBigText: true,
          children: [
            {
              text: '1-я смена',
              alignHeader: 'center',
              isBigText: true,
              children: [
                {
                  text: 'Наряд-\nзадание',
                  border: true,
                  textMinify: 'mini',
                  value: 'gr_cars',
                  alignHeader: 'center'
                },
                {
                  text: 'Расчет-\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'gr_pv',
                  alignHeader: 'center'
                },
                {
                  text: 'Текущая\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'gr_cs',
                  alignHeader: 'center'
                },
                {
                  text: 'Факт',
                  border: true,
                  textMinify: 'mini',
                  value: 'por_cars',
                  alignHeader: 'center'
                }
              ]
            },
            {
              text: '2-я смена',
              alignHeader: 'center',
              isBigText: true,
              children: [
                {
                  text: 'Наряд-\nзадание',
                  border: true,
                  textMinify: 'mini',
                  value: 'por_pl',
                  alignHeader: 'center'
                },
                {
                  text: 'Расчет-\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'por_pv',
                  alignHeader: 'center'
                },
                {
                  text: 'Текущая\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'por_cs',
                  alignHeader: 'center'
                },
                {
                  text: 'Факт',
