                  value: 'nrp_cars',
                  alignHeader: 'center'
                },
                {
                  text: 'Расчет-\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'usl_length',
                  alignHeader: 'center'
                },
                {
                  text: 'Текущая\nзаставка',
                  border: true,
                  textMinify: 'mini',
                  value: 'priority',
                  alignHeader: 'center'
                },
                {
                  text: 'Факт',
                  border: true,
