    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      const [year, month, day] = this.$store.state.filtersValues['date'].split(
        '-'
      );
      this.timeTitle =
        `${day}.${month}.${year}` +
        ' ' +
        this.$store.state.filtersValues['time_rep_2'] +
        ':00';
      this.tableTitle = true;
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: this.$store.state.filtersValues.drl
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/work_station_detail.json'
          : '/arm-isujt' + '/server/fakeReports_2_1_fr_test.json'
      ); //пока тестовые данные
    },
    setTimeDefaultTimeFilterData() {
      let nowdate = new Date();
      let nowhours = nowdate.getHours();
      let nowMinutes = nowdate.getMinutes();
      if (nowhours < 6) {
        nowdate = new Date(nowdate.setHours(0, 0, 0).valueOf() - 86400000);
        this.type = 0;
        this.time = 18;
      } else if (nowhours >= 6 && nowhours < 18) {
        this.time = 6;

        if (nowhours === 6 && nowMinutes <= 50) {
          this.type = 1;
          this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[0];
        } else {
          this.type = 0;
          this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[1];
        }
      } else if (nowhours >= 18) {
        this.time = 18;

        if (nowhours === 18 && nowMinutes <= 50) {
          this.type = 1;
          this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[0];
        } else {
          this.type = 0;
          this.$store.state.timeFilterCurrentValue = this.$store.state.timeFilterValues[1];
        }
      }

      nowdate.setHours(0, 0, 0);
    }
