//Моделирование. Депо
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" :downloadUrl="downloadUrl" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: 'Район управления ДРЛ',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/drl.json'
            : '/arm-isujt' + '/server/drl.json',
          keyText: 'name',
          keyValue: 'id'
        },
        {
          filterName: 'calc',
          filterType: 'select',
          labelText: 'Расчет',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/CalculationList.json'
            : '/arm-isujt' + '/server/CalculationList.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :headers="headers"
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
        ref="table"
        :tableType="tableType"
      />
      <Buttons :params="params" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';

export default {
  name: 'ModelingDepo',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/norm_depo.json',
      tableType: 'norm_depo',
      params: {
        buttons: {
          print: true,
          download: true
        },
        name: 'Моделирование',
        icon: 'icon icon-trending-up'
      },
      downloadUrl:
        '/ajax.php?module=ExcelController&method=rep_operative_technique_pdf',
      tabModel: 1,
      headers: [
        {
          text: '№',
          align: 'start',
          value: 'sort_number',
          textMinify: 'mini',
          border: true
        },
        {
          text: 'Депо приписки локомотивов',
          align: 'start',
          alignHeader: 'center',
          value: 'depot_name',
          textMinify: 'mini',
          width: '200',
          border: true,
          rightBorder: true
        },
        {
          text: 'Потребное количество эксплуатируемого\nпарка локомотивов на 2',
          align: 'start',
          cols: 4,
          alignHeader: 'center',
          children: [
            {
              text: 'Т (4-х)',
              value: 's',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Т (3-х)',
              value: 't',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Л (2-х)',
              value: 'l',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Всего',
              value: 'sum',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>
