//Планирование. Задание тех.станций
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: 'Дата' },
        {
          filterName: 'road',
          filterType: 'select',
          labelText: 'Дорога',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/road_filter.json'
            : '/arm-isujt' + '/server/road_filter.json',
          keyText: 'name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <div class="page-subtitle">
      План работы технических станций Восточный полигон на
      <span class="page-subtitle-bold">{{ getDate }}</span>
    </div>
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <DataTable
        :url="getUrl"
        :params="{ date: Math.floor(Date.now() / 1000), road: 0, load: false }"
        ref="table"
        :tableType="tableType"
        :hasDetail="true"
        :hasInfo="true"
        @loadDetail="setDetail"
        :isRemote="true"
      />
      <Buttons :params="params" />
    </div>
    <DetailPanel :isRemote="true" ref="detailPanel" />
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import DataTable from '../../components/DataTable';
import Buttons from '../../components/buttons/Buttons';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'PlanningTechStationTasks',
  components: {
    Title,
    Links,
    Filters,
    Tabs,
    DataTable,
    Buttons,
    DetailPanel
  },
  created() {
    this.$store.state.detailInfo.detailName = 'Форма раскрытия';
  },
  computed: {
    getDate() {
      let d =
        this.$store.state.filtersValues.date ||
        new Date().toISOString().substr(0, 10);
      const [year, month, day] = d.split('-');
      return `${day}.${month}.${year}`;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        road: this.$store.state.filtersValues.road
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail(
        this.$store.state.isTestMode
          ? '../..' + '/server/work_station_detail.json'
          : '/arm-isujt' + '/server/work_station_detail.json'
      ); //пока тестовые данные
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/work_station.json',
      tableType: 'st_tasks',
      params: {
        buttons: {
          print: true,
          titleTop: 30
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 0,
      detailHeaders: []
    };
  }
};
</script>
