<template>
  <div
    class="v-data-table v-data-table--dense v-data-table--fixed-height theme--light"
  >
    <div class="v-data-table__wrapper">
      <table class="v-data-table-body">
        <thead class="v-data-table-header">
          <tr v-for="(row, i) in createHeaderArray()" :key="i">
            <th
              v-for="(h, id) in row"
              :key="id"
              :colspan="
                i === 0 && id === 'tableName'
                  ? Object.keys(tableData[1]).length - 1
                  : 0
              "
              :rowspan="i === 0 && id === 'name' ? 2 : 1"
              :class="{ 'thin-border-bottom': i === 0 && id === 'tableName' }"
            >
              {{ i === 1 && id !== 'name' ? h.split('(').join('\n(') : h }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, i) in tableData[2]"
            :key="i"
            @click="selectRow(i)"
            :class="{ 'selected-row': i === selectedItem }"
          >
            <td
              v-for="(cols, col) in item"
              :key="col"
              :class="{
                'edit-cell-main':
                  col !== 'name' && i !== tableData[2].length - 1,
                'edited-cell-main': cellClasses[i + '_' + col]
              }"
            >
              <EditCell
                @updateCell="changeClass($event, item[col], item, col, i)"
                :oldValue="item[col]"
                :row="item"
                :col="col"
                :rowId="i"
                v-if="col !== 'name' && i !== tableData[2].length - 1"
              />
              <div
                v-else
                :style="{ width: col === 'name' ? '150px' : 'auto' }"
                :class="{
                  'number-sum': col !== 'name' && i === tableData[2].length - 1
                }"
              >
                {{ item[col] }}
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import EditCell from './inputs/EditCell';
export default {
  name: 'DynamicTable',
  components: {
    EditCell
  },
  props: ['tableData'],
  data() {
    return {
      selectedItem: null,
      editedValues: [],
      cellClasses: {}
    };
  },
  methods: {
    changeClass(val, oldVal, it, col, i) {
      if (!this.cellClasses[i + '_' + col]) {
        this.cellClasses[i + '_' + col] = true;
      } else {
        if (+val === oldVal) {
          delete this.cellClasses[i + '_' + col];
        }
      }
      this.changeValue(val, col.value, i, oldVal);
      this.$forceUpdate();
    },
    changeValue(value, col, id, oldValue) {
      let isValueExist;
      this.editedValues.forEach((editedValue, i, arr) => {
        if (editedValue.id === id && editedValue.col === col) {
          if (+value !== +oldValue) {
            editedValue.value = value;
          } else {
            arr.splice(i);
          }
          isValueExist = true;
        }
      });
      if (!isValueExist) {
        this.editedValues.push({ value, col, id });
      }
    },
    selectRow(rowNumber) {
      this.selectedItem = rowNumber;
    },
    createHeaderArray() {
      let secondHeaderRow = Object.assign({}, this.tableData[1]);
      delete secondHeaderRow[' name'];
      return [
        {
          name: this.tableData[1][' name'],
          tableName: this.tableData[0]
        },
        secondHeaderRow
      ];
    }
  }
};
</script>

