//Планирование. График оборота локомотивов
<template>
  <div class="pa-5 pb-0">
    <Title :params="params" />
    <Links :params="params" />
    <Filters
      :filters="[
        {
          filterName: 'version',
          filterType: 'select',
          labelText: 'Версия',
          inputWidth: '260px'
        },
        {
          filterName: 'station',
          filterType: 'select',
          labelText: 'Станция',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: this.$store.state.isTestMode
            ? '../..' + '/server/stations.json'
            : '/arm-isujt' + '/server/stations.json',
          keyText: 'long_name',
          keyValue: 'id'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <div
      class="table-container"
      :class="{
        'pb-15':
          params.buttons.recalc || params.buttons.save || params.buttons.cancel
      }"
    >
      <GrafTable :url="getUrl" />
      <Buttons :params="params" />
      <DetailPanel :tableType="tableType" />
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title';
import Links from '../../components/buttons/Links';
import Filters from '../../components/Filters';
import Tabs from '../../components/Tabs';
import Buttons from '../../components/buttons/Buttons';
import GrafTable from '../../components/GrafTable';
import DetailPanel from '../../components/DetailPanel';

export default {
  name: 'PlanningGraf',
  components: {
    DetailPanel,
    Title,
    Links,
    Filters,
    Tabs,
    Buttons,
    GrafTable
  },
  created() {
    this.$store.state.detailInfo.detailName =
      'Легенда ГОЛ (график оборота локомотива на суточном горизонте)';
  },
  computed: {
    getDate() {
      const [year, month, day] = this.$store.state.filtersValues.date.split(
        '-'
      );
      return `${day}.${month}.${year}`;
    },
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date)
      });
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      defaultUrl: '/server/temp.json',
      tableType: 'graf_loco',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 6,
      headers: []
    };
  }
};
</script>
