<template>
  <v-card
    v-resize="onResize"
    id="table"
    class="table"
    elevation="0"
    :class="{
      'slide-leave slide-enter-active': this.$store.state.sheet && hasDetail,
      'slide-leave-to slide-leave-active': !this.$store.state.sheet && hasDetail
    }"
    :style="getTableHeight"
  >
    <div id="svg"></div>
    <v-progress-circular
      v-if="progress"
      class="progress-circle"
      indeterminate
      color="#00194D"
    ></v-progress-circular>
    <v-dialog v-model="dialog" width="450" persistent>
      <v-card>
        <v-toolbar height="80" elevation="0">
          Схема обновлена
          <v-btn
            color="#D2DAE7"
            class="close-dialog"
            icon
            :ripple="false"
            absolute
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-actions class="justify-center">
          <v-btn
            class="close"
            color="#4D6C99"
            min-height="29px"
            small
            dark
            depressed
            @click="dialog = false"
            :ripple="false"
            >OK</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import axios from 'axios';
import * as d3 from 'd3';
import 'd3-selection-multi';

export default {
  name: 'Svg_report',
  props: ['hasDetail', 'url'],
  data() {
    return {
      windowSize: {
        x: 0,
        y: 0
      },
      dialog: false,
      progress: false,
      staticDiagramDataUrl: '../../server/fakeReports_06_diagram_points.json',
      dx: 200,
      dy: 150,

      styleFontHead: 16,
      styleFontStation: 12,
      styleTableFontSize: 12,
      styleTableFontSizeIns: 12,

      styleColorHead: '#00194D',
      styleColorStation: '#00194D', //надпись название станции
      styleColorPoint: '#EE3129', //точка - станция
      styleColorLine: '#EE3129',
      styleControlLine: '#4D6C99',
      styleColorRect: '#32AFA5',
      styleBackgroundColorRect: '#FDFFEA',
      styleColorRectBetween: 'blue',
      styleBackgroundColorRectBetween: '#F4FFFE'
    };
  },
  computed: {
    getTableHeight() {
      const FILTERS_CONTENT_HEIGHT = 138,
        BLOCKS_HEIGHT = 276,
        DETAIL_HEIGHT = 65,
        OPEN_DETAIL_CONTENT = 190;
      let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

      if (this.hasDetail) tableHeight -= DETAIL_HEIGHT;

      if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
      if (this.$store.state.sheet) tableHeight -= OPEN_DETAIL_CONTENT;

      return `height: ${tableHeight}px`;
    }
  },
  methods: {
    formatDate(date) {
      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    fetchData() {
      this.progress = true;
      axios.get(this.url).then((response) => {
        let data = {};

        if (response.data.svg1) {
          data.svg1 = response.data.svg1;
        }
        if (response.data.svg2) {
          data.svg2 = response.data.svg2;
        }
        this.progress = false;
        this.dialog = true;
        let parseData = {};
        parseData.svg1 = this.parsingData(data.svg1);
        parseData.svg2 = this.parsingData(data.svg2);
        //очистка холста
        this.clear();

        //размеры нового холста
        let x = 3258;
        let y = 2115;
        d3.select('#svg').append('svg').attr('width', x).attr('height', y);

        //смещение, чтобы двигать все объекты сразу на холсте
        let dx = this.dx;
        let dy = this.dy;

        //верхний заголовок
        this.text(
          {
            style: 'font: ' + this.styleFontHead + 'px FSRAILBook;',
            fill: this.styleColorHead,
            'text-anchor': 'start',
            x: dx - 160,
            y: -120 + dy
          },
          'Нормативное время нахождения локомотивов на участках Восточного полигона на '
        );
        this.text(
          {
            style: 'font: ' + this.styleFontHead + 'px FSRAILBold;',
            fill: this.styleColorHead,
            'text-anchor': 'start',
            x: 490 + dx,
            y: -120 + dy
          },
          (this.$store.state.filtersValues['type_rep_6'] ? '18:00 ' : '6:00 ') +
            this.formatDate(this.$store.state.filtersValues.date)
        );

        //линия-разделитель
        this.line({
          stroke: this.styleControlLine,
          'stroke-width': '2',
          x1: dx - 160,
          y1: 927 + dy,
          x2: 3000 + dx,
          y2: 927 + dy
        });

        //нижний заголовок
        this.text(
          {
            style: 'font: ' + this.styleFontHead + 'px FSRAILBook;',
            fill: this.styleColorHead,
            'text-anchor': 'start',
            x: dx - 160,
            y: 954 + dy
          },
          'Нормативное время нахождения тепловозов на участках северного хода  ДВЖД на '
        );
        this.text(
          {
            style: 'font: ' + this.styleFontHead + 'px FSRAILBold;',
            fill: this.styleColorHead,
            'text-anchor': 'start',
            x: 480 + dx,
            y: 954 + dy
          },
          (this.$store.state.filtersValues['type_rep_6'] ? '18:00 ' : '6:00 ') +
            this.formatDate(this.$store.state.filtersValues.date)
        );

        //пунктирные линии
        // this.line({
        //   stroke: '#4D6C99',
        //   opacity: 0.3,
        //   'stroke-width': '2',
        //   'stroke-dasharray': '5, 10',
        //   x1: dx,
        //   y1: -80 + dy,
        //   x2: dx,
        //   y2: 400 + dy
        // });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 536 + dx,
          y1: -73 + dy,
          x2: 536 + dx,
          y2: 647 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 1106 + dx,
          y1: -73 + dy,
          x2: 1106 + dx,
          y2: 647 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 1435 + dx,
          y1: -73 + dy,
          x2: 1435 + dx,
          y2: 647 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 2203 + dx,
          y1: -73 + dy,
          x2: 2203 + dx,
          y2: 647 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 2487 + dx,
          y1: -73 + dy,
          x2: 2487 + dx,
          y2: 647 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 192 + dx,
          y1: 1006 + dy,
          x2: 192 + dx,
          y2: 1826 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 1247 + dx,
          y1: 1006 + dy,
          x2: 1247 + dx,
          y2: 1480 + dy
        });
        this.line({
          stroke: '#4D6C99',
          opacity: 0.3,
          'stroke-width': '2',
          'stroke-dasharray': '5, 10',
          x1: 1822 + dx,
          y1: 1006 + dy,
          x2: 1822 + dx,
          y2: 1480 + dy
        });
        //
        // this.line({
        //   stroke: '#4D6C99',
        //   opacity: 0.3,
        //   'stroke-width': '2',
        //   'stroke-dasharray': '5, 10',
        //   x1: 315 + dx,
        //   y1: 550 + dy,
        //   x2: 315 + dx,
        //   y2: 950 + dy
        // });
        // this.line({
        //   stroke: '#4D6C99',
        //   opacity: 0.3,
        //   'stroke-width': '2',
        //   'stroke-dasharray': '5, 10',
        //   x1: 545 + dx,
        //   y1: 550 + dy,
        //   x2: 545 + dx,
        //   y2: 950 + dy
        // });
        // this.line({
        //   stroke: '#4D6C99',
        //   opacity: 0.3,
        //   'stroke-width': '2',
        //   'stroke-dasharray': '5, 10',
        //   x1: 725 + dx,
        //   y1: 550 + dy,
        //   x2: 725 + dx,
        //   y2: 950 + dy
        // });

        axios.get(this.staticDiagramDataUrl).then((resp) => {
          let jsonData = resp.data;

          //рисуем точки и названия станций
          for (let jd_key in jsonData) {
            let po = jsonData[jd_key];
            let po_prev = null; //сохраним предыдущую точку
            for (let key in po) {
              this.circle({
                fill: this.styleColorPoint,
                r: 5,
                cx: po[key]['cx'] + dx,
                cy: po[key]['cy'] + dy
              });

              //поддержка рисования линий между станциями
              //line=line - свойство для построения линии, начиная с предыдущей точки
              if (po[key]['line'] === 'line') {
                if (po_prev == null) {
                  console.log(
                    'Svg_report06.draw: невозможно нарисовать линию, т.к. не установлена начальная точка.'
                  );
                } else {
                  this.line({
                    stroke: this.styleColorLine,
                    'stroke-width': '2',
                    x1: po_prev['cx'] + dx,
                    y1: po_prev['cy'] + dy,
                    x2: po[key]['cx'] + dx,
                    y2: po[key]['cy'] + dy
                  });
                }
              }
              //сохраним настройки этой точки в объекте - point=point - свойство для фиксации данных, для построения линий по последующим точкам
              if (po[key]['point'] === 'point') {
                po_prev = po[key];
              }

              //текст - сместим вверх и влево на 5 px
              this.text(
                {
                  style: 'font: ' + this.styleFontStation + 'px FSRAILBook;',
                  fill: this.styleColorStation,
                  'text-anchor': 'start',
                  x: po[key]['cx'] + dx + 5,
                  y: po[key]['cy'] + dy - 5
                },
                po[key]['text']
              );
            }
          }

          //рисование таблиц - после всего остального
          //сразу сопоставляем с данными из объекта data
          let text1 = '';
          let text2 = '';
          let text3 = '';
          let text4 = '';
          let request1 = '';
          let request1_1 = '';
          let request1_2 = '';
          let request1_3 = '';
          let request2 = '';
          let request3 = '';
          let request4 = '';

          for (let jd_key in jsonData) {
            let po = jsonData[jd_key];
            let po_prev = null; //сохраним предыдущую точку
            let po_prev_use = null; //предыдущая точка (сохраним последнюю использовавшуюся точку) - только для работы с данными для определения участка пути
            for (let key in po) {
              //заплатка для отмены блокировки построения таблицы при отсутствии данных
              text1 = '';
              text2 = '';
              text3 = '';
              text4 = '';
              request1 = '';
              request1_1 = '';
              request1_2 = '';
              request1_3 = '';
              request2 = '';
              request3 = '';
              request4 = '';
              if (po[key]['esr']) {
                if (data[jd_key]) {
                  if (data[jd_key][po[key]['esr']]) {
                    if (data[jd_key][po[key]['esr']]['rezCD'])
                      text1 = data[jd_key][po[key]['esr']]['rezCD'];
                    if (data[jd_key][po[key]['esr']]['norm'])
                      text2 = data[jd_key][po[key]['esr']]['norm'];
                    if (data[jd_key][po[key]['esr']]['fact'])
                      text3 = data[jd_key][po[key]['esr']]['fact'];
                    if (data[jd_key][po[key]['esr']]['avg'])
                      text4 = data[jd_key][po[key]['esr']]['avg'];
                    if (data[jd_key][po[key]['esr']]['count_loco'])
                      text1 = data[jd_key][po[key]['esr']]['count_loco'];
                    if (data[jd_key][po[key]['esr']]['rezCD_request'])
                      request1 = data[jd_key][po[key]['esr']]['rezCD_request'];
                    if (data[jd_key][po[key]['esr']]['norm_request'])
                      request2 = data[jd_key][po[key]['esr']]['norm_request'];
                    if (data[jd_key][po[key]['esr']]['fact_request'])
                      request3 = data[jd_key][po[key]['esr']]['fact_request'];
                    if (data[jd_key][po[key]['esr']]['avg_request'])
                      request4 = data[jd_key][po[key]['esr']]['avg_request'];
                    if (
                      data[jd_key][po[key]['esr']]['count_loco'] instanceof
                      Object
                    ) {
                      if (
                        data[jd_key][po[key]['esr']]['count_loco'][
                          'rezCD_request'
                        ]
                      )
                        request1_1 =
                          data[jd_key][po[key]['esr']]['count_loco'][
                            'rezCD_request'
                          ];
                      if (
                        data[jd_key][po[key]['esr']]['count_loco']['ep_request']
                      )
                        request1_2 =
                          data[jd_key][po[key]['esr']]['count_loco'][
                            'ep_request'
                          ];
                      if (
                        data[jd_key][po[key]['esr']]['count_loco'][
                          'nep_request'
                        ]
                      )
                        request1_3 =
                          data[jd_key][po[key]['esr']]['count_loco'][
                            'nep_request'
                          ];
                    }
                  }
                }
              }

              //таблица
              if (po[key]['table']) {
                let params = po[key]['table'];
                params.x = params.dx + po[key]['cx'];
                params.y = params.dy + po[key]['cy'];
                params.cx = po[key]['cx'];
                params.cy = po[key]['cy'];
                params.typeLine = 'table';
                this.table({
                  rect: params,
                  fontSize: this.styleTableFontSize,
                  fontSizeIns: this.styleTableFontSizeIns,
                  colorLine: '#F5F5B4',
                  backgroundColor: this.styleBackgroundColorRect,
                  dx: dx,
                  dy: dy,
                  text1: text1,
                  text2: text2,
                  text3: text3,
                  text4: text4,
                  request1: request1,
                  request2: request2,
                  request3: request3,
                  request4: request4
                });
              }

              //первая точка сюда точно не пройдет
              if (po_prev == null) {
                console.log(
                  'Svg_report06.draw: невозможно нарисовать таблицу midtable, т.к. не установлена начальная точка.'
                );
              } else {
                if (po[key]['midtable']) {
                  let midtables = po[key]['midtable'];
                  for (let midkey in midtables) {
                    //заплатка для отмены блокировки построения таблицы при отсутствии данных
                    text2 = '';
                    text3 = '';
                    text4 = '';
                    if (po[key]['esr']) {
                      if (parseData[jd_key][po[key]['esr']]) {
                        if (parseData[jd_key][po[key]['esr']][midkey]) {
                          text2 =
                            parseData[jd_key][po[key]['esr']][midkey]['norm'];
                          text3 =
                            parseData[jd_key][po[key]['esr']][midkey]['fact'];
                          text4 =
                            parseData[jd_key][po[key]['esr']][midkey]['avg'];
                        }
                      }
                    }
                    let params = midtables[midkey];
                    params.x = params.dx + po[key]['cx'];
                    params.y = params.dy + po[key]['cy'];
                    params.cx = (po[key]['cx'] + po_prev['cx']) / 2; //вычисляем середину отрезка line
                    params.cy = (po[key]['cy'] + po_prev['cy']) / 2;
                    params.typeLine = 'midtable';

                    this.table({
                      rect: params,
                      fontSize: this.styleTableFontSize,
                      fontSizeIns: this.styleTableFontSizeIns,
                      colorLine: this.styleColorRect,
                      backgroundColor: this.styleBackgroundColorRectBetween,
                      dx: dx,
                      dy: dy,
                      text1: params.titletext,
                      text2: text2,
                      text3: text3,
                      text4: text4
                    });
                  }
                }
              }
              //сохраним настройки этой точки в объекте - point=point - свойство для фиксации данных, для построения линий по последующим точкам
              if (po[key]['point'] === 'point') {
                po_prev = po[key];
              }
              po_prev_use = po[key];
            }
            console.log(po_prev_use);
            console.log(request1_1);
            console.log(request1_2);
            console.log(request1_3);
          }
        });
      });
    },
    parsingData: function (data) {
      let newData = {};

      if (data) {
        let keys = [];
        for (let key in data) {
          keys = key.split('-');

          if (keys.length === 2) {
            if (!newData[keys[1]]) newData[keys[1]] = {};
            newData[keys[1]][keys[0]] = {};

            newData[keys[1]][keys[0]] = {
              name: data[key]['name'],
              norm: data[key]['norm'],
              fact: data[key]['fact'],
              avg: data[key]['avg']
            };
          }
        }
      }

      return newData;
    },

    clear: function () {
      let selector = '#svg';

      d3.select(selector).selectAll('svg').remove();
      d3.select(selector + 'Number')
        .selectAll('svg')
        .remove();
      d3.select(selector + 'Time')
        .selectAll('svg')
        .remove();
    },

    //рисование линий
    line: function (params) {
      let canvas = d3.select('#svg svg');
      let line = canvas.append('line');
      for (let attr in params) {
        line.attr([attr], params[attr]);
      }
    },

    circle: function (params) {
      let canvas = d3.select('#svg svg');
      let circle = canvas.append('circle');
      for (let attr in params) {
        circle.attr([attr], params[attr]);
      }
    },

    text: function (params, text) {
      let canvas = d3.select('#svg svg');
      let title = canvas.append('text').text(text);
      for (let attr in params) {
        title.attr([attr], params[attr]);
      }
    },
    //рисование таблиц + выносных линий от условных точек
    table: function (params) {
      let rect = params.rect, //объект со свойствами для рисования
        fontSize = params.fontSize,
        fontSizeIns = params.fontSizeIns,
        colorLine = params.colorLine,
        backgroundColor = params.backgroundColor,
        dx = params.dx,
        dy = params.dy,
        text1 = params.text1, //текст для подстановки
        text2 = params.text2,
        text3 = params.text3,
        text4 = params.text4,
        request1 = params.request1,
        request2 = params.request2,
        request3 = params.request3,
        request4 = params.request4;

      let canvas = d3.select('#svg svg');
      if (!rect) {
        console.log(
          'Svg.table: не определены параметры таблицы. Невозможно построить таблицу'
        );
        return;
      }
      if (!fontSize) fontSize = 10;
      if (!fontSizeIns) fontSizeIns = fontSize;
      if (!colorLine) colorLine = 'black';
      if (!backgroundColor) backgroundColor = 'white';
      if (!dx) dx = 0;
      if (!dy) dy = 0;
      if (!text1) text1 = '';
      if (!text2) text2 = '';
      if (!text3) text3 = '';
      if (!text4) text4 = '';
      let color = '';

      let returns = {};

      canvas
        .append('rect')
        .attr('id', 'myTable')
        .attr('stroke', colorLine)
        .attr('stroke-width', '1')
        .attr('fill', backgroundColor)
        .attr('x', rect['x'] + dx)
        .attr('y', rect['y'] + dy)
        .attr('rx', '5')
        .attr('width', rect['width'])
        .attr('height', rect['height']);

      if (rect.type === 'cols3') {
        let lineX = rect['width'] / 3;
        let lineY = rect['height'] / 4;

        //1 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + dx)
          .attr('y1', rect['y'] + lineY + dy)
          .attr('x2', rect['x'] + lineX * 3 + dx)
          .attr('y2', rect['y'] + lineY + dy);
        //2 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + lineX + dx)
          .attr('y1', rect['y'] + lineY * 2 + dy)
          .attr('x2', rect['x'] + lineX * 3 + dx)
          .attr('y2', rect['y'] + lineY * 2 + dy);
        //3 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + dx)
          .attr('y1', rect['y'] + lineY * 3 + dy)
          .attr('x2', rect['x'] + lineX * 3 + dx)
          .attr('y2', rect['y'] + lineY * 3 + dy);
        //4 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + lineX + dx)
          .attr('y1', rect['y'] + lineY + dy)
          .attr('x2', rect['x'] + lineX + dx)
          .attr('y2', rect['y'] + lineY * 3 + dy);
        //5 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + lineX * 2 + dx)
          .attr('y1', rect['y'] + dy)
          .attr('x2', rect['x'] + lineX * 2 + dx)
          .attr('y2', rect['y'] + lineY * 4 + dy);

        //текст
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 2) / 2 + dx)
          .attr('y', rect['y'] + lineY / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('Наличие лок (рез ЦД)');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 3) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 3) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('норма');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 3) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 5) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('факт');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 2) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 7) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('ср.суточный оборот');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 3) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('общий');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 4) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('оборот');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 5) / 2 + dy + fontSize / 3)
          .attr('fill', this.styleColorHead)
          .text('станции');

        //textsvg_rezCD - класс, чтобы можно было схватить элемент извне (вне холста)
        color = 'fill:#00194D;';
        if (request1) {
          color = 'fill:#1056EB; cursor:pointer;';
        }
        if (text1 instanceof Object) {
          if (text1.ep_request) {
            color = 'fill:#1056EB; cursor:pointer;';
          }
          returns.text1_2 = canvas
            .append('text')
            .attr('class', 'textsvg_loco_ep')
            .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
            .attr('text-anchor', 'middle')
            .attr('x', rect['x'] + (lineX * 5) / 2 + dx - 18)
            .attr('y', rect['y'] + lineY / 2 + dy + fontSizeIns / 3)
            .text(text1.ep);
          color = 'fill:#00194D;';

          if (text1.nep_request) {
            color = 'fill:#1056EB; cursor:pointer;';
          }
          returns.text1_3 = canvas
            .append('text')
            .attr('class', 'textsvg_loco_nep')
            .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
            .attr('text-anchor', 'middle')
            .attr('x', rect['x'] + (lineX * 5) / 2 + dx)
            .attr('y', rect['y'] + lineY / 2 + dy + fontSizeIns / 3)
            .text(text1.nep);
          color = 'fill:#00194D;';

          if (text1.rezCD_request) {
            color = 'fill:#1056EB; cursor:pointer;';
          }
          returns.text1_1 = canvas
            .append('text')
            .attr('class', 'textsvg_loco_rezCD')
            .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
            .attr('text-anchor', 'middle')
            .attr('x', rect['x'] + (lineX * 5) / 2 + dx + 18)
            .attr('y', rect['y'] + lineY / 2 + dy + fontSizeIns / 3)
            .text(text1.rezCD);
          color = 'fill:#00194D;';
        } else {
          returns.text1 = canvas
            .append('text')
            .attr('class', 'textsvg_rezCD')
            .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
            .attr('text-anchor', 'middle')
            .attr('x', rect['x'] + (lineX * 5) / 2 + dx)
            .attr('y', rect['y'] + lineY / 2 + dy + fontSizeIns / 3)
            .text(text1);
        }

        color = 'fill:#00194D;';
        if (request2) {
          color = 'fill:#1056EB;  cursor:pointer;';
        }
        returns.text2 = canvas
          .append('text')
          .attr('class', 'textsvg_norm')
          .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 5) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 3) / 2 + dy + fontSizeIns / 3)
          .text(text2);

        color = 'fill:#00194D;';
        if (request3) {
          color = 'fill:#1056EB;  cursor:pointer;';
        }
        returns.text3 = canvas
          .append('text')
          .attr('class', 'textsvg_fact')
          .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 5) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 5) / 2 + dy + fontSizeIns / 3)
          .text(text3);

        color = 'fill:#00194D;';
        if (request4) {
          color = 'fill:#1056EB; cursor:pointer;';
        }
        returns.text4 = canvas
          .append('text')
          .attr('class', 'textsvg_avg')
          .attr('style', color + 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 5) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 7) / 2 + dy + fontSizeIns / 3)
          .text(text4);
      } else if (rect.type === 'cols2') {
        let lineX = rect['width'] / 2;
        let lineY = rect['height'] / 5;

        //1 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + dx)
          .attr('y1', rect['y'] + lineY * 2 + dy)
          .attr('x2', rect['x'] + lineX * 2 + dx)
          .attr('y2', rect['y'] + lineY * 2 + dy);
        //2 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + dx)
          .attr('y1', rect['y'] + lineY * 3 + dy)
          .attr('x2', rect['x'] + lineX * 2 + dx)
          .attr('y2', rect['y'] + lineY * 3 + dy);
        //3 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + dx)
          .attr('y1', rect['y'] + lineY * 4 + dy)
          .attr('x2', rect['x'] + lineX * 2 + dx)
          .attr('y2', rect['y'] + lineY * 4 + dy);
        //4 линия
        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['x'] + lineX + dx)
          .attr('y1', rect['y'] + lineY * 2 + dy)
          .attr('x2', rect['x'] + lineX + dx)
          .attr('y2', rect['y'] + lineY * 5 + dy);

        //текст
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 2) / 2 + dx)
          .attr('y', rect['y'] + lineY / 2 + dy + fontSize / 3)
          .text('оборот лок.');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 5) / 2 + dy + fontSize / 3)
          .text('норма');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 7) / 2 + dy + fontSize / 3)
          .text('факт');
        canvas
          .append('text')
          .attr('style', 'font: ' + fontSize + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + lineX / 2 + dx)
          .attr('y', rect['y'] + (lineY * 9) / 2 + dy + fontSize / 3)
          .text('ср.суточ.');

        //текст для подстановки
        returns.text1 = canvas
          .append('text')
          .attr('style', 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 2) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 3) / 2 + dy + fontSizeIns / 3)
          .text(text1);
        returns.text2 = canvas
          .append('text')
          .attr('style', 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 3) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 5) / 2 + dy + fontSizeIns / 3)
          .text(text2);
        returns.text3 = canvas
          .append('text')
          .attr('style', 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 3) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 7) / 2 + dy + fontSizeIns / 3)
          .text(text3);
        returns.text4 = canvas
          .append('text')
          .attr('style', 'font: ' + fontSizeIns + 'px FSRAILBook;')
          .attr('fill', this.styleColorHead)
          .attr('text-anchor', 'middle')
          .attr('x', rect['x'] + (lineX * 3) / 2 + dx)
          .attr('y', rect['y'] + (lineY * 9) / 2 + dy + fontSizeIns / 3)
          .text(text4);
      } else {
        console.log(
          'Svg.table: не определен тип таблицы. Невозможно построить таблицу.'
        );
      }

      //рисование выносной линии
      if (rect.extens_line) {
        let point2_x = 0;
        let point2_y = 0;

        if (rect.width + rect.dx >= 0 && rect.height + rect.dy >= 0) {
          point2_x = rect.x;
          point2_y = rect.y;
        } else if (rect.width + rect.dx >= 0 && rect.height + rect.dy < 0) {
          point2_x = rect.x;
          point2_y = rect.y + rect.height;
        } else if (rect.width + rect.dx < 0 && rect.height + rect.dy >= 0) {
          point2_x = rect.x + rect.width;
          point2_y = rect.y;
        } else if (rect.width + rect.dx < 0 && rect.height + rect.dy < 0) {
          point2_x = rect.x + rect.width;
          point2_y = rect.y + rect.height;
        }

        canvas
          .append('line')
          .attr('stroke', colorLine)
          .attr('stroke-width', '1')
          .attr('x1', rect['cx'] + dx)
          .attr('y1', rect['cy'] + dy)
          .attr('x2', point2_x + dx)
          .attr('y2', point2_y + dy);
      }

      return returns;
    }
  }
};
</script>

