<template>
  <v-btn
    class="add-table mr-1"
    color="#4D6C99"
    height="25px"
    small
    dark
    depressed
    :ripple="false"
    @click="[addRecordToTable(), $emit('forceUpdateTable')]"
  >
    Добавить
  </v-btn>
</template>

<script>
export default {
  name: 'AddTableButton',
  props: ['record'],
  methods: {
    addRecordToTable() {
      if (this.$store.state.tableStores['rep_control_0']) {
        this.$store.state.tableStores['rep_control_0'].push(this.record);
      } else {
        this.$store.state.tableStores['rep_control_0'] = [this.record];
      }
    }
  }
};
</script>

<style scoped lang="sass">
button.add-table
  width: 80px
  padding-top: 2px !important
  font: 12px/14px $body-font-family
  letter-spacing: 0.01px
  text-transform: none
</style>
